import React, { useState, useEffect, useContext } from "react";

import { animateScroll as scroll } from "react-scroll";

import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination, A11y } from "swiper";
import "swiper/swiper-bundle.min.css";
import "swiper/swiper.min.css";

import ProductSlider from "../components/ProductSlider";

const SliderRelatedProducts = () => {
  const condition = true;
  const [relatedProductsArray, setRelatedProductsArray] = useState([]);
  const selectedProduct =
    JSON.parse(sessionStorage.getItem("selectedProduct")) || "";

  useEffect(() => {
    const getProductCategories = async () => {
      try {
        const pathRes = await fetch(
          `https://api.mercadolibre.com/categories/${selectedProduct.category_id}`
        );
        const pathData = await pathRes.json();

        const path_root =
          pathData.path_from_root[2] &&
          (pathData.path_from_root[2].name === "Juegos de Sommier y Colchón" ||
            pathData.path_from_root[2].name === "Colchones" ||
            pathData.path_from_root[2].name === "Heladeras" ||
            pathData.path_from_root[2].name === "Mobiliario para Cocinas" ||
            pathData.path_from_root[2].name === "Sillas Gamer" ||
            pathData.path_from_root[2].name === "Estufas y Calefactores")
            ? pathData.path_from_root[2].name
            : pathData.path_from_root[3] &&
              (pathData.path_from_root[3].name === "Discos Rígidos y SSDs" ||
                pathData.path_from_root[3].name === "Griferías Convencionales")
            ? pathData.path_from_root[3].name
            : pathData.path_from_root[4] &&
              pathData.path_from_root[4].name === "Fundas para Valijas"
            ? pathData.path_from_root[4].name
            : pathData.path_from_root[1] && pathData.path_from_root[1].name;

        const url = `https://api.mercadolibre.com/sites/MLA/search?q=${path_root}`;

        const response = await fetch(url);
        const data = await response.json();

        const products = data.results.slice(0, 20); // Esto tomará solo los primeros 20 elementos del array 'data.results'.

        const productDetailsArray = await Promise.all(
          products.map((product) => fetchProductDetails(product.id))
        );

        setRelatedProductsArray(productDetailsArray.slice(0, 20));
      } catch (error) {
        console.log(error);
      }
    };

    getProductCategories();
  }, []);

  const fetchProductDetails = async (productId) => {
    const url = `https://api.mercadolibre.com/items/${productId}`;
    const response = await fetch(url);
    const data = await response.json();
    return data;
  };

  const conversorMoneda = (valor, moneda) => {
    if (isNaN(valor)) {
      return "";
    }
    const formattedValue = `$${Math.floor(valor).toLocaleString("es-AR")}`;
    return formattedValue;
  };

  const calculateDiscountPercentage = (originalPrice, price) => {
    if (originalPrice !== null && price !== null) {
      const discount = originalPrice - price;
      const discountPercentage = (discount / originalPrice) * 100;
      return discountPercentage.toFixed(0);
    }
    return "";
  };

  const handleClick = () => {
    scroll.scrollTo("productDetailsContainer", {
      duration: 500, // Duración del desplazamiento en milisegundos
      smooth: true, // Desplazamiento suave
      offset: -100, // Desplazamiento adicional (opcional)
    });
  };

  return (
    <>
      {relatedProductsArray.length > 0 ? (
        <>
          <div className="sliderRelatedProducts_container">
            <h5 className="relatedProducts-text">Productos Relacionados</h5>
            <Swiper
              modules={[Navigation, Pagination, A11y]}
              spaceBetween={20}
              slidesPerView={5}
              navigation={true}
              allowTouchMove={false}
              pagination={{ clickable: true }}
              breakpoints={{
                1440: {
                  slidesPerView: 5,
                },
                1300: {
                  slidesPerView: 5,
                },
                1200: {
                  slidesPerView: 4,
                },

                1100: {
                  slidesPerView: 4,
                },
              }}
              className="style-swiper_relatedProducts"
            >
              {relatedProductsArray.map((product) => (
                <SwiperSlide
                  className="style-swiperSlide_relatedProducts"
                  key={product.id}
                  onClick={handleClick}
                >
                  <ProductSlider
                    is_sliderRelatedProducts="slider_productos_relatedProducts"
                    slider_relatedProduct={product}
                    function_conversorMoneda={conversorMoneda}
                    function_calculateDiscountPercentage={
                      calculateDiscountPercentage
                    }
                    useSwiperSlide={condition}
                    key={product.id}
                  />
                </SwiperSlide>
              ))}
            </Swiper>
          </div>
          {/******** SLIDER VERSION MOBILE ********/}
          <div className="title-sliderRelatedProducts_container">
            <h5 className="relatedProducts-text">Productos Relacionados</h5>
            <div className="sliderRelatedProducts_mobile">
              <div className="slidesRelProd_mobile">
                {relatedProductsArray.map((product) => (
                  <div
                    className="slideRelProd_content_mobile"
                    key={product.id}
                    onClick={handleClick}
                  >
                    <ProductSlider
                      is_sliderProducts="slider_productos"
                      slider_product={product}
                      function_conversorMoneda={conversorMoneda}
                      function_calculateDiscountPercentage={
                        calculateDiscountPercentage
                      }
                      useSwiperSlide={condition}
                      key={product.id}
                    />
                  </div>
                ))}
              </div>
            </div>
          </div>
          {/***************************************/}
        </>
      ) : (
        <div
          className="spinner-border spinner-loading d-block m-auto"
          role="status"
        >
          <span className="sr-only">Loading...</span>
        </div>
      )}
    </>
  );
};

export default SliderRelatedProducts;
