import React from "react";

const BuyProductDetails = ({
  is_selectedProduct,
  selectedProduct,
  is_productArray,
  item,
  is_onlyOneProduct,
  onlyOneProduct,
}) => {
  function handle_productInfo() {
    let product;

    if (is_selectedProduct === "is_selectedProduct") {
      product = selectedProduct;
    }

    if (is_productArray === "is_productArray") {
      product = item;
    }
    if (is_onlyOneProduct === "is_onlyOneProduct") {
      product = onlyOneProduct;
    }

    return product;
  }

  return (
    <div className="productImageContainer" key={handle_productInfo().id}>
      <div className="product-image_container">
        <img src={handle_productInfo().secure_thumbnail} alt="" />
      </div>
      <span className="product-title_addProduct">
        {handle_productInfo().title}
      </span>
      <span className="addedProduct-quantity">
        {`Cantidad: ${handle_productInfo().products_quantity}`}
      </span>
    </div>
  );
};

export default BuyProductDetails;
