import React from "react";

const ProductsPagination = ({
  array_pages,
  page,
  previousPage,
  nextPage,
  handle_next_previous_page,
}) => {
  return (
    <div className="pagination_maincontainer">
      <div className="pagination_content">
        {array_pages.length > 1 && (
          <button className="previous-page mr-3" onClick={previousPage}>
            <span className="paginationText_button">{"< Anterior"}</span>
            <i className="fa-solid fa-chevron-left custom-planification-arrow"></i>
          </button>
        )}
        <div className="nums-pagination">
          {array_pages.map((pag) => (
            <button
              className={`${pag === page && "fill_backgroundColor border-0"}`}
              onClick={() => handle_next_previous_page(pag)}
              key={pag}
            >
              {pag}
            </button>
          ))}
        </div>
        {array_pages.length > 1 && (
          <button className="next-page ml-3" onClick={nextPage}>
            <span className="paginationText_button">{"Siguiente >"}</span>
            <i className="fa-solid fa-chevron-right custom-planification-arrow"></i>
          </button>
        )}
      </div>
    </div>
  );
};

export default ProductsPagination;
