import React, { useState, useContext } from "react";

import { AppContext } from "../../context/MyProvider";

import Slider from "../Slider";
import SliderProducts from "../SliderProducts";
import SliderCategories from "../SliderCategories";
import Footer from "../Footer";

const Home = () => {
  const provider = useContext(AppContext);

  const [productArray_slider, setProductArray_slider] = useState([]);

  const getProductArray = (evento) => {
    setProductArray_slider(evento);
  };

  return (
    <div
      className="home_mainContainer"
      onClick={() => provider.setIsOpen(false)}
    >
      <Slider />
      <SliderCategories />
      <SliderProducts getProductArray={getProductArray} />
      {productArray_slider.length > 0 && <Footer />}
    </div>
  );
};

export default Home;
