import React, { useContext } from "react";
import { NavLink, useNavigate } from "react-router-dom";

import { AppContext } from "../context/MyProvider";
import BurgerMenu from "./BurgerMenu";
import mawStore_logo from "../assets/mawstore-logo.png";

import cart from "../assets/carts/cart2.png";
import user_icon from "../assets/user-icon2.png";

const Header = ({ handleSearchedProduct }) => {
  const provider = useContext(AppContext);
  const navigate = useNavigate();
  const countProductArray =
    JSON.parse(localStorage.getItem("cart-productArray")) || [];

  const blockBackToHome =
    JSON.parse(sessionStorage.getItem("blockBackToHome")) || "";

  const handle_searchProduct = (e) => {
    e.preventDefault();
    sessionStorage.setItem("searchedProduct", provider.producto);

    handleSearchedProduct(provider.producto);

    setTimeout(() => {
      provider.setProducto("");
    }, 1000);

    navigate(`/listado/${provider.producto}`);
  };

  const handleMawIcon = (route) => {
    provider.setActive_itemNavBar(0);
    provider.setActive_checkoutCart(!true);
    provider.setIsOpen(false);
    navigate(route);
  };

  const handleCartIcon = () => {
    sessionStorage.removeItem("selectedProduct");

    setTimeout(() => {
      navigate("/shopping-cart");
    }, 700);
  };

  return (
    <>
      <div className="burgerMenu_container" style={{ display: "none" }}>
        <BurgerMenu />
      </div>
      <nav
        className="header_main-container navbar"
        onClick={() => {
          provider.setActivate_optionsContainer(false);
          provider.setIsOpen(false);
        }}
      >
        <section className="mawIcon-form_container">
          <div className={blockBackToHome && "blockBack_to_home"}>
            <img
              className="mawStore_logo"
              onClick={() => handleMawIcon("/")}
              src={mawStore_logo}
              alt=""
            />
          </div>
          <form>
            <input
              type="text"
              size="60"
              maxLength="60"
              placeholder="Buscar productos"
              onChange={(e) => provider.setProducto(e.target.value)}
              value={provider.producto}
            />
            <NavLink to={`/listado/${provider.producto}`}>
              <button
                type="submit"
                onClick={handle_searchProduct}
                className="material-icons search-button"
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="21"
                  height="21"
                  fill="currentColor"
                  className="bi bi-search"
                  viewBox="0 0 16 16"
                >
                  <path d="M11.742 10.344a6.5 6.5 0 1 0-1.397 1.398h-.001c.03.04.062.078.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1.007 1.007 0 0 0-.115-.1zM12 6.5a5.5 5.5 0 1 1-11 0 5.5 5.5 0 0 1 11 0z" />
                </svg>
              </button>
            </NavLink>
          </form>
        </section>
        <section className="myAccount_shoppingCart">
          <div className="text-logo_myAccount_container">
            <div className="user-icon_container">
              <img src={user_icon} alt="" />
            </div>
            <span className="text-myAccount">Anónimo</span>
          </div>
          <span className="linea-vertical">|</span>
          <button
            className="image-carro_button"
            onClick={() => provider.setActive_itemNavBar(0)}
          >
            <img src={cart} alt="" onClick={handleCartIcon} />
            <span
              className={`text-cant-products ${
                countProductArray.length === 0 ? "opacity-0" : "opacity-100"
              }`}
            >
              {countProductArray.length}
            </span>
          </button>
        </section>
        <section className="header_bottom_container">
          <ul>
            <li>
              <NavLink
                to="/listado/audio-y-video"
                className={`style-navLink ${
                  provider.active_itemNavBar === 1 && "active_fill_navBar"
                }`}
              >
                Audio y Video
              </NavLink>
            </li>
            <li>
              <NavLink
                to="/listado/celulares"
                className={`style-navLink ${
                  provider.active_itemNavBar === 2 && "active_fill_navBar"
                }`}
              >
                Celulares
              </NavLink>
            </li>
            <li>
              <NavLink
                to="/listado/tecnologia"
                className={`style-navLink ${
                  provider.active_itemNavBar === 3 && "active_fill_navBar"
                }`}
              >
                Tecnología
              </NavLink>
            </li>
            <li>
              <NavLink
                to="/listado/electrodomesticos"
                className={`style-navLink ${
                  provider.active_itemNavBar === 4 && "active_fill_navBar"
                }`}
              >
                Electrodomésticos
              </NavLink>
            </li>
            <li>
              <NavLink
                to="/listado/construccion"
                className={`style-navLink ${
                  provider.active_itemNavBar === 5 && "active_fill_navBar"
                }`}
              >
                Construccion
              </NavLink>
            </li>
            <li>
              <NavLink
                to="/listado/ropa-y-accesorios"
                className={`style-navLink ${
                  provider.active_itemNavBar === 6 && "active_fill_navBar"
                }`}
              >
                Ropa y Accesorios
              </NavLink>
            </li>
            <li>
              <NavLink
                to="/listado/deportes-y-fitness"
                className={`style-navLink ${
                  provider.active_itemNavBar === 7 && "active_fill_navBar"
                }`}
              >
                Deportes y Fitness
              </NavLink>
            </li>
          </ul>
        </section>
      </nav>
    </>
  );
};

export default Header;
