import React, { useState, useEffect, useContext } from "react";
import { AppContext } from "../context/MyProvider";
import { useParams } from "react-router-dom";
import Product from "./Product";
import ProductsPagination from "./ProductsPagination";
import Footer from "../components/Footer";
import Loading from "./Loading";
import FilterProductPrices from "./FilterProductPrices";
import FilterProductCondition from "./FilterProductCondition";
import FilterProductShipping from "./FilterProductShipping";

const ProductOfferArray = ({ is_arrayParams, is_array_bannerImages }) => {
  const provider = useContext(AppContext);
  const { category } = useParams();
  const [array_itemsProducts, setArray_itemsProducts] = useState([]);
  const [page, setPage] = useState(1);
  const [productsPerPage, setProductsPerPage] = useState(15);
  const [maximoPerPage, setMaximoPerPage] = useState("");
  const [array_pages, setArray_pages] = useState([]);
  const [loading, setLoading] = useState(false);
  const [bannerImage, setBannerImage] = useState(null); // Nuevo estado para almacenar la imagen del banner

  const [defaultProducts, setDefaultProducts] = useState("");
  const [productData_array, setProductData_array] = useState("");
  const [productCondition, setProductCondition] = useState("");

  const handle_bannerImage = () => {
    let banner;
    if (category === "calefaccion") {
      banner = is_array_bannerImages[0];
    }
    if (category === "electrodomesticos&smart-tv") {
      banner = is_array_bannerImages[1];
    }

    if (category === "tecnologia") {
      banner = is_array_bannerImages[2];
    }

    if (category === "planchita&secador-de-pelo-atma") {
      banner = is_array_bannerImages[3];
    }

    if (category === "sillas-y-muebles") {
      banner = is_array_bannerImages[4];
    }

    if (category === "herramientas") {
      banner = is_array_bannerImages[5];
    }

    if (category === "abrigos") {
      banner = is_array_bannerImages[6];
    }

    return banner;
  };

  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true);

        const api_URL_category = array_params();
        if (api_URL_category === "") return;

        const response = await fetch(api_URL_category);
        const data = await response.json();

        const products = data.results;

        setProductData_array(products);

        sessionStorage.setItem("productFullData", JSON.stringify(data));

        const productDetailsArray = await Promise.all(
          products.map((product) => fetchProductDetails(product.id))
        );

        setDefaultProducts(productDetailsArray);
        setArray_itemsProducts(productDetailsArray);

        let maximo = productDetailsArray.length / productsPerPage;
        const roundedMaximo = Math.ceil(maximo);
        setMaximoPerPage(roundedMaximo);

        const pages = [];
        for (let i = 0; i < roundedMaximo; i++) {
          pages.push(i + 1);
        }
        setArray_pages(pages);

        setLoading(false);
      } catch (error) {
        console.log(error);
        setLoading(false);
      }
    };

    const array_params = () => {
      let result = "";

      is_arrayParams.forEach((param) => {
        if (param === category) {
          result = `https://api.mercadolibre.com/sites/MLA/search?q=${param}&condition=new&promotion=highlights`;
        }
      });
      return result;
    };

    fetchData();
  }, [category]);

  useEffect(() => {
    // Cargar la imagen del banner cuando se monta el componente
    setBannerImage(handle_bannerImage());
  }, []);

  const fetchProductDetails = async (productId) => {
    const url = `https://api.mercadolibre.com/items/${productId}`;
    const response = await fetch(url);
    const data = await response.json();
    setProductCondition(data);
    return data;
  };

  const conversorMoneda = (valor, moneda) => {
    if (isNaN(valor)) {
      return "";
    }

    const formattedValue = `$${Math.floor(valor).toLocaleString("es-AR")}`;
    return formattedValue;
  };

  const calculateDiscountPercentage = (originalPrice, price) => {
    if (originalPrice !== null && price !== null) {
      const discount = originalPrice - price;
      const discountPercentage = (discount / originalPrice) * 100;
      return discountPercentage.toFixed(0);
    }
    return "";
  };

  const nextPage = () => {
    if (page < maximoPerPage) {
      setTimeout(() => {
        setPage(page + 1);
      }, 100);
    }
  };

  const previousPage = () => {
    if (page > 1) {
      setTimeout(() => {
        setPage(page - 1);
      }, 100);
    }
  };

  const handle_next_previous_page = (pag) => {
    setTimeout(() => {
      setPage(pag);
    }, 100);
  };

  const handleFilterProductPrices = (e) => {
    if (e === "Por defecto") {
      setArray_itemsProducts(defaultProducts);
    }

    if (e === "Mas vendido") {
      const bestSellingSortedArray = [...array_itemsProducts].sort(
        (a, b) => b.sold_quantity - a.sold_quantity
      );

      setArray_itemsProducts(bestSellingSortedArray);
    }

    if (e === "Menor precio") {
      const ascendingSortedArray = [...array_itemsProducts].sort(
        (a, b) => a.price - b.price
      );

      setArray_itemsProducts(ascendingSortedArray);
    }

    if (e === "Mayor precio") {
      const descendingSortedArray = [...array_itemsProducts].sort(
        (a, b) => b.price - a.price
      );
      setArray_itemsProducts(descendingSortedArray);
    }
  };

  const handleFilterProductCondition = (e) => {
    if (e === "Por defecto") {
      setArray_itemsProducts(defaultProducts);
    }

    if (e === "new") {
      const ProductsOrderedConditionNew = defaultProducts.filter(
        (item) => item.condition === "new"
      );

      setArray_itemsProducts(ProductsOrderedConditionNew);
    }

    if (e === "used") {
      const ProductsOrderedConditionUsed = defaultProducts.filter(
        (item) => item.condition === "used"
      );
      setArray_itemsProducts(ProductsOrderedConditionUsed);
    }
  };

  const handleFilterProductShipping = (e) => {
    setTimeout(() => {
      if (e === "envío gratis") {
        const productsOrderedFreeShipping = array_itemsProducts.filter(
          (item) => item.shipping.free_shipping === true
        );
        setArray_itemsProducts(productsOrderedFreeShipping);
      }
      if (e === "envío por defecto") {
        setArray_itemsProducts(defaultProducts);
      }
    }, 800);
  };

  const handleFilterIcon = () => {
    setTimeout(() => {
      provider.setIsActiveFilterMode(!false);
      provider.setHideProductContainer(!false);
    }, 300);
  };

  return (
    <>
      {array_itemsProducts.length > 0 ? (
        <>
          <div
            style={{ backgroundColor: "#F4F4F4" }}
            onClick={() => provider.setIsOpen(false)}
          >
            <div
              className={`banner_container banner-mobile ${
                bannerImage === is_array_bannerImages[0]
                  ? "custom-banner-1"
                  : bannerImage === is_array_bannerImages[1]
                  ? "custom-banner-2"
                  : bannerImage === is_array_bannerImages[2]
                  ? "custom-banner-3"
                  : bannerImage === is_array_bannerImages[3]
                  ? "custom-banner-4"
                  : bannerImage === is_array_bannerImages[4]
                  ? "custom-banner-5"
                  : bannerImage === is_array_bannerImages[5]
                  ? "custom-banner-6"
                  : bannerImage === is_array_bannerImages[6]
                  ? "custom-banner-7"
                  : ""
              }`}
              onClick={() => provider.setActivate_optionsContainer(false)}
            >
              <img src={bannerImage} alt="" />
            </div>

            {/******* VERSIÓN MOBILE *******/}
            <div className="filter-icon-text_container">
              <div className="filter-content" onClick={handleFilterIcon}>
                <button className="filter-button">
                  <i className="fa-solid fa-filter"></i>
                </button>
                <span>Filtrar</span>
              </div>
            </div>
            {provider.isActiveFilterMode && (
              <div className="filterConditionContent_mobile">
                <div className="backArrow-filterTitle_container">
                  <button
                    className="backArrow_button"
                    onClick={() => {
                      provider.setIsActiveFilterMode(false);
                      provider.setHideProductContainer(false);
                    }}
                  >
                    <i className="fa-solid fa-arrow-left"></i>
                  </button>
                  <h5 className="filter-title">Filtrar por</h5>
                </div>
                <div className="filters_content_mobile">
                  <FilterProductShipping
                    handleFilterProductShipping={handleFilterProductShipping}
                  />
                  <FilterProductPrices
                    handleFilterProductPrices={handleFilterProductPrices}
                  />
                  {productCondition.condition === "used" && (
                    <FilterProductCondition
                      handleFilterProductCondition={
                        handleFilterProductCondition
                      }
                    />
                  )}
                </div>
              </div>
            )}
            {/******************************/}

            <div className="filters_container">
              <FilterProductShipping
                handleFilterProductShipping={handleFilterProductShipping}
              />
              {productCondition.condition === "used" && (
                <FilterProductCondition
                  handleFilterProductCondition={handleFilterProductCondition}
                />
              )}
              <FilterProductPrices
                handleFilterProductPrices={handleFilterProductPrices}
              />
            </div>
            {!provider.hideProductContainer ? (
              <>
                <div
                  className="producto_mainContainer"
                  onClick={() => provider.setActivate_optionsContainer(false)}
                >
                  <div className="filterProduct-flexbox-container">
                    <div className="product-flexbox">
                      {array_itemsProducts
                        .slice(
                          (page - 1) * productsPerPage,
                          (page - 1) * productsPerPage + productsPerPage
                        )
                        .map((item) => (
                          <div className="product-content" key={item.id}>
                            <Product
                              productData_array={productData_array}
                              is_productWithOffer="producto_con_oferta"
                              product_with_offer={item}
                              function_conversorMoneda={conversorMoneda}
                              function_calculateDiscountPercentage={
                                calculateDiscountPercentage
                              }
                              key={item.id}
                            />
                          </div>
                        ))}
                    </div>
                  </div>

                  <ProductsPagination
                    page={page}
                    array_pages={array_pages}
                    previousPage={previousPage}
                    nextPage={nextPage}
                    handle_next_previous_page={handle_next_previous_page}
                  />
                </div>
                <Footer />
              </>
            ) : (
              ""
            )}
          </div>
        </>
      ) : (
        <Loading />
      )}
    </>
  );
};

export default ProductOfferArray;
