import React, { useState, useEffect, useContext, useRef } from "react";
import { useNavigate } from "react-router-dom";
import { AppContext } from "../context/MyProvider";
import mawstoreIcon from "../assets/mawstore-logo3.png";
import html2canvas from "html2canvas";

const Voucher = () => {
  const voucherRef = useRef(null); // Referencia al elemento contenedor

  const { setActive_checkoutCart, conversorMoneda, product_currency } =
    useContext(AppContext);
  const navigate = useNavigate();

  const [productArray, setProductArray] = useState([]);
  const [customerObject, setCustomerObject] = useState("");
  const [onlyOneProduct, setOnlyOneProduct] = useState({});
  const [firstName, setFirstName] = useState("");
  const [selectedProduct, setSelectedProduct] = useState("");

  const cartProductArray =
    JSON.parse(localStorage.getItem("cart-productArray")) || [];

  useEffect(() => {
    const disableBackButton = (event) => {
      event.preventDefault();
      navigate("/checkout/cart/buying/voucher"); // Reemplaza con la ruta a la que quieres redirigir al usuario
    };

    window.history.pushState(null, document.title, window.location.href);
    window.addEventListener("popstate", disableBackButton);

    return () => {
      window.removeEventListener("popstate", disableBackButton);
    };
  }, [navigate]);

  useEffect(() => {
    setActive_checkoutCart(true);

    const selectedProduct =
      JSON.parse(sessionStorage.getItem("selectedProduct")) || "";

    const tempProductArray =
      JSON.parse(sessionStorage.getItem("productArray")) || [];
    const tempCustomerObject =
      JSON.parse(sessionStorage.getItem("customerObject")) || "";
    const tempOnlyOneProduct =
      JSON.parse(sessionStorage.getItem("added_product")) || "";

    setProductArray(tempProductArray);
    setCustomerObject(tempCustomerObject);
    setOnlyOneProduct(tempOnlyOneProduct);
    setSelectedProduct(selectedProduct);

    const words = tempCustomerObject.customerFullName.split(" ");
    if (words.length > 0) {
      const firstWord = words[0];
      setFirstName(firstWord);
    }
  }, [setActive_checkoutCart]);

  const handle_buttonBackHome = () => {
    if (productArray.length !== 0 && !selectedProduct) {
      localStorage.clear();
    }

    if (onlyOneProduct !== "" && !selectedProduct) {
      sessionStorage.clear("");

      const filteredProductArray = cartProductArray.filter(
        (item) => item.id !== onlyOneProduct.id
      );

      localStorage.setItem(
        "cart-productArray",
        JSON.stringify(filteredProductArray)
      );
    }

    setActive_checkoutCart(false);
    navigate("/");
    sessionStorage.removeItem("blockBackToHome");
  };

  const handleDownloadImage = () => {
    const voucher = voucherRef.current;

    html2canvas(voucher).then((canvas) => {
      // Crea un elemento <a> temporal para descargar la imagen
      const link = document.createElement("a");
      link.href = canvas.toDataURL();
      link.download = `factura-mawstore-${firstName}`;
      link.click();
    });
  };

  return (
    <div className="voucher-mainContainer">
      <div className="voucher_firstContainer" ref={voucherRef}>
        <div className="mawstoreIcon_container">
          <img src={mawstoreIcon} alt="" />
        </div>
        <div className="voucher-content">
          <div className="subtitle-voucherDate_container d-flex justify-content-between">
            <h5 className="confirmation-title">¡Tu compra fue exitosa!</h5>

            <div className="date-number-voucher_container d-flex flex-column">
              <span className="voucher-number">{`Factura: #${customerObject.randomNumber}`}</span>
              <span className="currentDate_voucher">
                {`Fecha de emisión: ${customerObject.currentDate}`}
              </span>
            </div>
          </div>

          <section className="voucher_topContainer">
            <div className="greetText">
              <span className="helloText">Hola</span>
              <span className="nameText">
                <b>{` ${firstName},`}</b>
              </span>
            </div>
            <p>
              Ya estamos preparando tu pedido. Espero lo disfrutes. ¡Muchas
              gracias por tu compra!
            </p>
          </section>
          <section className="voucher_bottomContainer">
            <span className="buyDetails_text">Detalles de la compra</span>
            <table id="voucher-table" className="custom-table">
              <thead>
                <tr>
                  <th>Cantidad</th>
                  <th>Producto</th>
                  <th>Monto</th>
                </tr>
              </thead>
              <tbody>
                {selectedProduct ? (
                  <tr className="custom-tr_tbody">
                    <td>{selectedProduct.products_quantity}</td>
                    <td>{selectedProduct.title}</td>
                    <td>
                      {conversorMoneda(selectedProduct.price, product_currency)}
                    </td>
                  </tr>
                ) : productArray.length !== 0 && onlyOneProduct === "" ? (
                  productArray.map((item) => (
                    <tr className="custom-tr_tbody" key={item.id}>
                      <td>{item.products_quantity}</td>
                      <td>{item.title}</td>
                      <td>
                        {conversorMoneda(item.totalPrice, product_currency)}
                      </td>
                    </tr>
                  ))
                ) : onlyOneProduct !== "" ? (
                  <tr className="custom-tr_tbody">
                    <td>{onlyOneProduct.products_quantity}</td>
                    <td>{onlyOneProduct.title}</td>
                    <td>
                      {conversorMoneda(
                        onlyOneProduct.totalPrice,
                        product_currency
                      )}
                    </td>
                  </tr>
                ) : null}
                <tr className="custom-tr_totalPrice">
                  <td className="no-border-right"></td>
                  <td className="no-border-right">
                    <b>Total: </b>
                  </td>
                  <td className="custom-td-border no-border-right">
                    <span className="totalPrice_buy">
                      <b>
                        {selectedProduct
                          ? conversorMoneda(
                              selectedProduct.price,
                              product_currency
                            )
                          : productArray.length !== 0 && onlyOneProduct === ""
                          ? conversorMoneda(
                              customerObject.totalCostBuy,
                              product_currency
                            )
                          : onlyOneProduct !== ""
                          ? conversorMoneda(
                              onlyOneProduct.totalPrice,
                              product_currency
                            )
                          : ""}
                      </b>
                    </span>
                  </td>
                </tr>
              </tbody>
            </table>
          </section>
        </div>
      </div>
      <section className="buttons-container">
        <button
          className="custom-backHomebutton"
          onClick={handle_buttonBackHome}
        >
          Volver a Inicio
        </button>
        <button
          className="custom-downloadImage ml-3"
          onClick={handleDownloadImage}
        >
          Descargar factura
          <i className="fa-solid fa-file-invoice-dollar ml-2"></i>
        </button>
      </section>
    </div>
  );
};

export default Voucher;
