import React, { createContext, useState } from "react";

export const AppContext = createContext({});

export default function MyProvider({ children }) {
  const [producto, setProducto] = useState("");
  let [searchedProduct, setSearchedProduct] = useState("");
  const [object_URLs_categories, setObject_URLs_categories] = useState({});
  const [active_itemNavBar, setActive_itemNavBar] = useState(0);
  let [array_productsCart, setArray_productsCart] = useState([]);
  const [product_currency, setProduct_currency] = useState(null);
  let [count_productQuantity, setCount_productQuantity] = useState(0);
  const [active_checkoutCart, setActive_checkoutCart] = useState(false);
  const [resumenProductQuantity, setResumenProductQuantity] = useState([]);
  const [productTotalCost, setProductTotalCost] = useState(0);
  const [addedProduct, setAddedProduct] = useState({});
  const [active_onlyOneProduct, setActive_onlyOneProduct] = useState(false);
  const [isOpen, setIsOpen] = useState(false);
  const [activate_loadingPoster, setActivate_loadingPoster] = useState(false);
  const [activate_optionsContainer, setActivate_optionsContainer] =
    useState(false);

  const [activate_menuFilterCondition, setActivate_menuFilterCondition] =
    useState(false);
  const [isActiveFilterMode, setIsActiveFilterMode] = useState(false);
  const [hideProductContainer, setHideProductContainer] = useState(false);

  const conversorMoneda = (valor, moneda) => {
    if (isNaN(valor)) {
      return "";
    }

    const formattedValue = `$${Math.floor(valor).toLocaleString("es-AR")}`;
    return formattedValue;
  };

  const calculateDiscountPercentage = (originalPrice, price) => {
    if (originalPrice !== null && price !== null) {
      const discount = originalPrice - price;
      const discountPercentage = (discount / originalPrice) * 100;
      return discountPercentage.toFixed(0);
    }
    return "";
  };

  return (
    <AppContext.Provider
      value={{
        object_URLs_categories,
        setObject_URLs_categories,
        producto,
        setProducto,
        active_itemNavBar,
        setActive_itemNavBar,
        searchedProduct,
        setSearchedProduct,
        array_productsCart,
        setArray_productsCart,
        product_currency,
        setProduct_currency,
        count_productQuantity,
        setCount_productQuantity,
        active_checkoutCart,
        setActive_checkoutCart,
        resumenProductQuantity,
        setResumenProductQuantity,
        conversorMoneda,
        calculateDiscountPercentage,
        productTotalCost,
        setProductTotalCost,
        addedProduct,
        setAddedProduct,
        active_onlyOneProduct,
        setActive_onlyOneProduct,
        isOpen,
        setIsOpen,
        activate_loadingPoster,
        setActivate_loadingPoster,
        activate_optionsContainer,
        setActivate_optionsContainer,
        activate_menuFilterCondition,
        setActivate_menuFilterCondition,
        isActiveFilterMode,
        setIsActiveFilterMode,
        hideProductContainer,
        setHideProductContainer,
      }}
    >
      {children}
    </AppContext.Provider>
  );
}
