import React, { useState, useContext } from "react";

import { AppContext } from "../context/MyProvider";

const FilterProductCondition = ({ handleFilterProductCondition }) => {
  const provider = useContext(AppContext);

  const [isFilterOn, setIsFilterOn] = useState(false);

  const [currentValue, setCurrentValue] = useState("");

  const options = [
    { value: "Por defecto", label: "Por defecto" },
    { value: "new", label: "Nuevo" },
    { value: "used", label: "Usado" },
    // Agrega más opciones según tus necesidades
  ];

  const handleActivate_filter_condition = () => {
    provider.setActivate_menuFilterCondition(
      !provider.activate_menuFilterCondition
    );
  };

  const handle_filterConditionValue = (item) => {
    setCurrentValue(item.label);
    handleFilterProductCondition(item.value);
    setIsFilterOn(!false);
    provider.setActivate_menuFilterCondition(false);
  };

  return (
    <div
      className="filter-product-condition_container"
      onClick={handleActivate_filter_condition}
    >
      <span className="order-text-condition">Condición</span>
      <div
        className="current-value-condition"
        onClick={handleActivate_filter_condition}
      >
        <span>{isFilterOn ? currentValue : options[0].label}</span>
      </div>

      <div className="valuesFilter-arrowButton_conditionContainer">
        <div
          className={`filterValues_conditionContainer ${
            provider.activate_menuFilterCondition &&
            "extend_filterConditionContainer"
          }`}
        >
          {options.map((item) => (
            <span
              key={item.value}
              value={item.value}
              onClick={() => {
                handle_filterConditionValue(item);
                provider.setIsActiveFilterMode(false);
              }}
              className={`option-value_condition ${
                currentValue === item.label && "paintedValueCondition"
              }`}
            >
              {item.label}
            </span>
          ))}
        </div>
        <button
          className={`arrow_button_condition ${
            provider.activate_menuFilterCondition && "rotateArrowContainer"
          }`}
        >
          <i className="fa-solid fa-chevron-down custom-arrowDown_condition"></i>
        </button>
      </div>
      <button
        className={`arrow_button_mobile_condition ${
          provider.activate_menuFilterCondition && "rotateArrow"
        }`}
      >
        <i className="fa-solid fa-chevron-down custom-arrowDown-mobile_condition"></i>
      </button>
    </div>
  );
};

export default FilterProductCondition;
