import React from "react";
import logoMAW from "../assets/mawLogo_footer.png";
import { animateScroll as scroll } from "react-scroll";

const Footer = () => {
  const handleClickScroll = () => {
    scroll.scrollTo("", {
      duration: 500, // Duración del desplazamiento en milisegundos
      smooth: true, // Desplazamiento suave
      offset: -100, // Desplazamiento adicional (opcional)
    });
  };

  return (
    <footer className="footer_container">
      <div className="footer-content">
        <section className="logoMAW-redes_container">
          <div className="logoMAW_container">
            <img src={logoMAW} alt="" />
          </div>
          <div className="redes_container">
            <a
              href="https://www.linkedin.com/in/marcelowasinger/"
              target="_blank"
              className="linkedIn_ancle"
            >
              <i className="fa-brands fa-linkedin-in"></i>
            </a>
            <a
              href="https://github.com/macheWasinger"
              target="_blank"
              className="github_ancle"
            >
              <i className="fa-brands fa-github"></i>
            </a>
            <a
              href="https://www.facebook.com/profile.php?id=100070072241358"
              target="_blank"
              className="facebook_ancle"
            >
              <i className="fa-brands fa-facebook"></i>
            </a>
            <a
              href="https://wa.me/541130911213"
              target="_blank"
              className="whatsapp_ancle"
            >
              <i className="fa-brands fa-whatsapp"></i>
            </a>
          </div>
        </section>
        <section className="infoAt-alCliente_mainContainer">
          <div className="content-buildingInfo at-cliente_container">
            <span className="titulo_at-cliente">Atención al cliente: </span>
            <span className="style-num">0800 123 4567</span>
            <div>
              <p>LU-VI de 09:00 a 18:00</p>
              <p>SA de 9:00 a 13:00</p>
              <p>contacto@mawstore.com</p>
            </div>
          </div>
          <div className="content-buildingInfo cobranza-creditos_container">
            <span className="titulo-cobranzas">Cobranza de créditos:</span>
            <p>cobranzas@mawstore.com.ar</p>
          </div>
          <div className="content-buildingInfo ventasTelef_container">
            <span className="titulo-ventasTelef">Venta telefónica:</span>
            <span className="style-num">0810 111 2222</span>
            <div>
              <p>LU-VI de 8:00 a 20:00</p>
              <p>SA-DO-Feriados 9:00 a 21:00</p>
            </div>
          </div>
        </section>
      </div>
      <div className="developer-data_container">
        <span className="custom-developerText">
          © 2022 Creado por Marcelo Adrián Wasinger
        </span>
        <button onClick={handleClickScroll} className="arrowUP_button">
          <i className="fa-solid fa-chevron-up"></i>
        </button>
      </div>
    </footer>
  );
};

export default Footer;
