import React from "react";
import { useNavigate } from "react-router-dom";
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from "react-responsive-carousel";

import banner1 from "../assets/banners/banner-1.webp";
import banner2 from "../assets/banners/banner-2.webp";
import banner3 from "../assets/banners/banner-3.webp";
import banner4 from "../assets/banners/banner-4.jpg";
import banner5 from "../assets/banners/banner-5.webp";
import banner6 from "../assets/banners/banner-6.webp";
import banner7 from "../assets/banners/banner-7.webp";

const Slider = () => {
  const navigate = useNavigate();
  const carouselStyles = {
    width: "100%",
  };

  const carouselProps = {
    showThumbs: false,
    infiniteLoop: true,
    autoPlay: true,
    showStatus: false,
  };

  const handleImageClick = (route) => {
    navigate(route);
  };

  const renderArrowPrev = (clickHandler, hasPrev) => {
    return (
      hasPrev && (
        <button
          className="carousel-custom-arrow carousel-custom-arrow-prev"
          onClick={clickHandler}
        >
          <i className="fas fa-chevron-left"></i>
        </button>
      )
    );
  };

  const renderArrowNext = (clickHandler, hasNext) => {
    return (
      hasNext && (
        <button
          className="carousel-custom-arrow carousel-custom-arrow-next"
          onClick={clickHandler}
        >
          <i className="fas fa-chevron-right"></i>
        </button>
      )
    );
  };

  return (
    <Carousel
      {...carouselProps}
      style={carouselStyles}
      className="carousel-mainContainer"
      renderArrowPrev={renderArrowPrev}
      renderArrowNext={renderArrowNext}
    >
      <div
        className="banner-mobile custom-banner-1"
        onClick={() => handleImageClick("/ofertas/calefaccion")}
      >
        <img src={banner1} alt="First slide" />
      </div>

      <div
        className="banner-mobile custom-banner-2"
        onClick={() => handleImageClick("/ofertas/electrodomesticos&smart-tv")}
      >
        <img src={banner2} alt="Second slide" />
      </div>

      <div
        className="banner-mobile custom-banner-3"
        onClick={() => handleImageClick("/ofertas/tecnologia")}
      >
        <img src={banner3} alt="Third slide" />
      </div>

      <div
        className="banner-mobile custom-banner-4"
        onClick={() =>
          handleImageClick("/ofertas/planchita&secador-de-pelo-atma")
        }
      >
        <img src={banner4} alt="Fourth slide" />
      </div>

      <div
        className="banner-mobile custom-banner-5"
        onClick={() => handleImageClick("/ofertas/sillas-y-muebles")}
      >
        <img src={banner5} alt="Fifth slide" />
      </div>

      <div
        className="banner-mobile custom-banner-6"
        onClick={() => handleImageClick("/ofertas/herramientas")}
      >
        <img src={banner6} alt="Sixth slide" />
      </div>

      <div
        className="banner-mobile custom-banner-7"
        onClick={() => handleImageClick("/ofertas/abrigos")}
      >
        <img src={banner7} alt="Seventh slide" />
      </div>
    </Carousel>
  );
};

export default Slider;
