import React, { useContext } from "react";
import { AppContext } from "../context/MyProvider";
import { useNavigate } from "react-router-dom";
import mawstoreIcon from "../assets/mawstore-logo.png";

const BurgerMenu = () => {
  const provider = useContext(AppContext);
  const navigate = useNavigate();

  const handleToggle = () => {
    provider.setIsOpen(!provider.isOpen);
  };

  const handle_navigateRoutes = (route) => {
    navigate(route);
    provider.setIsOpen(false);
  };

  const handleBackHome = (route) => {
    navigate(route);
    provider.setIsOpen(false);
    provider.setActive_itemNavBar(0);
  };

  return (
    <div className="container-Padre_menu">
      <div className={`burger ${provider.isOpen && "disable-padding"}`}>
        <input
          type="checkbox"
          id="btn-menu"
          checked={provider.isOpen}
          onClick={handleToggle}
        />
        {!provider.isOpen && (
          <label for="btn-menu" className="label-menu">
            <span id="span-1"></span>
            <span id="span-2"></span>
            <span id="span-3"></span>
          </label>
        )}
      </div>

      <nav
        className={`menu-navegacion ${
          provider.isOpen ? "menu-activado" : "hide"
        }`}
      >
        <div className="custom-mawstoreIcon">
          {provider.isOpen && (
            <label for="btn-menu" className="label-menu effect-labelMenu">
              <span id="span-1"></span>
              <span id="span-2"></span>
              <span id="span-3"></span>
            </label>
          )}
          <img src={mawstoreIcon} onClick={() => handleBackHome("/")} alt="" />
        </div>
        <ul>
          <li
            onClick={() => handle_navigateRoutes("/listado/audio-y-video")}
            className={`item-li item-1 ${
              provider.active_itemNavBar === 1 && "active_fill_navBar"
            }`}
          >
            Audio y Video
          </li>
          <li
            onClick={() => handle_navigateRoutes("/listado/celulares")}
            className={`item-li item-2 ${
              provider.active_itemNavBar === 2 && "active_fill_navBar"
            }`}
          >
            Celulares
          </li>
          <li
            onClick={() => handle_navigateRoutes("/listado/tecnologia")}
            className={`item-li item-3 ${
              provider.active_itemNavBar === 3 && "active_fill_navBar"
            }`}
          >
            Tecnología
          </li>
          <li
            onClick={() => handle_navigateRoutes("/listado/electrodomesticos")}
            className={`item-li item-4 ${
              provider.active_itemNavBar === 4 && "active_fill_navBar"
            }`}
          >
            Electrodomésticos
          </li>
          <li
            onClick={() => handle_navigateRoutes("/listado/construccion")}
            className={`item-li item-5 ${
              provider.active_itemNavBar === 5 && "active_fill_navBar"
            }`}
          >
            Construccion
          </li>
          <li
            onClick={() => handle_navigateRoutes("/listado/ropa-y-accesorios")}
            className={`item-li item-6 ${
              provider.active_itemNavBar === 6 && "active_fill_navBar"
            }`}
          >
            Ropa y Accesorios
          </li>
          <li
            onClick={() => handle_navigateRoutes("/listado/deportes-y-fitness")}
            className={`item-li item-7 ${
              provider.active_itemNavBar === 7 && "active_fill_navBar"
            }`}
          >
            Deportes y Fitness
          </li>
        </ul>
      </nav>
    </div>
  );
};

export default BurgerMenu;
