import React, { useState, useEffect } from "react";

import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, A11y } from "swiper";

import "swiper/css";

import ProductSlider from "./ProductSlider";

const SliderProducts = ({ getProductArray }) => {
  const condition = true;
  const [arrayPromocionesProducts, setArrayPromocionesProducts] = useState([]);
  const apiUrl = "https://api.mercadolibre.com/sites/MLA/search";
  const categories = ["MLA1005", "MLA1648", "MLA1051"]; // Solo Smart TV
  const promotionParam = "highlights";
  const [showSliderProd, setShowSliderProd] = useState(false);

  useEffect(() => {
    const tempShowSlider = true;
    setShowSliderProd(tempShowSlider);
  }, []);

  useEffect(() => {
    const getApiData = async () => {
      try {
        const allProducts = [];

        for (const category of categories) {
          const url = `${apiUrl}?category=${category}&condition=new&promotion=${promotionParam}`;

          const response = await fetch(url);
          const data = await response.json();

          const products = data.results;
          allProducts.push(...products);
        }

        const randomProducts = getRandomProducts(allProducts, 20);

        const productDetailsArray = await getProductDetails(
          randomProducts.map((product) => product.id)
        );
        setArrayPromocionesProducts(productDetailsArray);
        getProductArray(productDetailsArray);
      } catch (error) {
        console.log(error);
      }
    };

    const getRandomProducts = (products, count) => {
      const shuffledProducts = products.sort(() => Math.random() - 0.5);
      const selectedProducts = shuffledProducts.slice(0, count);
      return selectedProducts;
    };

    const fetchProductDetails = async (productId) => {
      try {
        const productDetailsUrl = `https://api.mercadolibre.com/items/${productId}`;
        const productDetailsRes = await fetch(productDetailsUrl);
        const productDetails = await productDetailsRes.json();
        return productDetails;
      } catch (error) {
        console.log(error);
        return null;
      }
    };

    const getProductDetails = async (productIds) => {
      try {
        const productDetailsArray = await Promise.all(
          productIds.map(async (productId) => {
            const productDetails = await fetchProductDetails(productId);
            return productDetails;
          })
        );

        const shuffledProductDetails = getRandomProducts(
          productDetailsArray,
          productDetailsArray.length
        );

        return shuffledProductDetails;
      } catch (error) {
        console.log(error);
        return [];
      }
    };

    getApiData();
  }, []);

  const conversorMoneda = (valor, moneda) => {
    if (isNaN(valor)) {
      return "";
    }
    const formattedValue = `$${Math.floor(valor).toLocaleString("es-AR")}`;
    return formattedValue;
  };

  const calculateDiscountPercentage = (originalPrice, price) => {
    if (originalPrice !== null && price !== null) {
      const discount = originalPrice - price;
      const discountPercentage = (discount / originalPrice) * 100;
      return discountPercentage.toFixed(0);
    }
    return "";
  };

  return (
    <>
      {arrayPromocionesProducts.length > 0 ? (
        <>
          <Swiper
            modules={[Navigation, A11y]}
            spaceBetween={20}
            slidesPerView={5}
            navigation={true}
            allowTouchMove={false}
            breakpoints={{
              1441: {
                slidesPerView: 5,
              },

              1440: {
                slidesPerView: 4,
              },
              1300: {
                slidesPerView: 4,
              },
              1200: {
                slidesPerView: 4,
              },

              1100: {
                slidesPerView: 4,
              },

              1081: {
                slidesPerView: 4,
              },

              1025: {
                slidesPerView: 3,
              },
            }}
            className="style-swiper"
          >
            <span className="title_ofertas_container">Ofertas destacadas</span>
            {arrayPromocionesProducts.map((product) => (
              <SwiperSlide className="style-swiperSlide" key={product.id}>
                <ProductSlider
                  is_sliderProducts="slider_productos"
                  slider_product={product}
                  function_conversorMoneda={conversorMoneda}
                  function_calculateDiscountPercentage={
                    calculateDiscountPercentage
                  }
                  useSwiperSlide={condition}
                  key={product.id}
                />
              </SwiperSlide>
            ))}
          </Swiper>

          {/******** SLIDER VERSION MOBILE ********/}
          <div className="title-sliderProducts_container">
            <span className="title_ofertas_container_mobile">
              Ofertas destacadas
            </span>
            {showSliderProd && (
              <div className="sliderProducts_mobile">
                <div className="slides_mobile">
                  {arrayPromocionesProducts.map((product) => (
                    <div className="slide_content_mobile" key={product.id}>
                      <ProductSlider
                        is_sliderProducts="slider_productos"
                        slider_product={product}
                        function_conversorMoneda={conversorMoneda}
                        function_calculateDiscountPercentage={
                          calculateDiscountPercentage
                        }
                        useSwiperSlide={condition}
                        key={product.id}
                      />
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
          {/***************************************/}
        </>
      ) : (
        <div className="spinner-container">
          <div className="spinner-border spinner-loading" role="status">
            <span className="sr-only">Loading...</span>
          </div>
        </div>
      )}
    </>
  );
};

export default SliderProducts;
