import React, { useState, useEffect, useContext } from "react";
import { useNavigate } from "react-router-dom";
import { AppContext } from "../context/MyProvider";
import emptyCart from "../assets/carts/empty-cart.png";
import Footer from "./Footer";

const ShoppingCart = () => {
  const navigate = useNavigate();
  const provider = useContext(AppContext);
  const cartProductArray =
    JSON.parse(localStorage.getItem("cart-productArray")) || [];

  let [products_totalCost, setProducts_totalCost] = useState(null);

  useEffect(() => {
    let tempProducts_totalCost = 0;

    cartProductArray.forEach((item) => {
      tempProducts_totalCost += item.totalPrice;
    });

    setProducts_totalCost(tempProducts_totalCost);
    provider.setActive_checkoutCart(!true);
  }, []);

  const conversorMoneda = (valor, moneda) => {
    if (isNaN(valor)) {
      return "";
    }

    const formattedValue = `$${Math.floor(valor).toLocaleString("es-AR")}`;
    return formattedValue;
  };

  const handleDeleteProduct = (selectedProduct) => {
    setProducts_totalCost(0);
    const filteredArrayProducts = cartProductArray.filter(
      (product) => product.id !== selectedProduct.id
    );

    localStorage.setItem(
      "cart-productArray",
      JSON.stringify(filteredArrayProducts)
    );

    provider.setArray_productsCart(filteredArrayProducts);

    const totalCost = filteredArrayProducts.reduce(
      (acc, current) => acc + current.totalPrice,
      0
    );

    setProducts_totalCost(totalCost);
  };

  const handleAddProduct = (selectedProduct) => {
    const updatedCart = cartProductArray.map((product) => {
      if (product.id === selectedProduct.id) {
        const updatedQuantity = product.products_quantity + 1;
        const pricePerUnit = product.totalPrice / product.products_quantity;
        const updatedPrice = pricePerUnit * updatedQuantity;

        return {
          ...product,
          products_quantity: updatedQuantity,
          totalPrice: updatedPrice,
        };
      }
      return product;
    });

    localStorage.setItem("cart-productArray", JSON.stringify(updatedCart));

    provider.setArray_productsCart(updatedCart);

    const totalCost = updatedCart.reduce(
      (acc, current) => acc + current.totalPrice,
      0
    );

    setProducts_totalCost(totalCost);
  };

  const handleDiscountProduct = (selectedProduct) => {
    const updatedCart = cartProductArray.map((product) => {
      if (product.id === selectedProduct.id) {
        const updatedQuantity = selectedProduct.products_quantity - 1;
        const pricePerUnit = product.totalPrice / product.products_quantity;
        const updatedPrice = pricePerUnit * updatedQuantity;

        return {
          ...product,
          products_quantity: updatedQuantity,
          totalPrice: updatedPrice,
        };
      }
      return product;
    });

    localStorage.setItem("cart-productArray", JSON.stringify(updatedCart));

    provider.setArray_productsCart(updatedCart);

    const totalCost = updatedCart.reduce(
      (acc, current) => acc + current.totalPrice,
      0
    );

    setProducts_totalCost(totalCost);
  };

  const handle_buyProduct = (product) => {
    sessionStorage.setItem("added_product", JSON.stringify(product));

    provider.setActivate_loadingPoster(true);

    setTimeout(() => {
      sessionStorage.setItem("active_onlyOneProduct", JSON.stringify(true));

      navigate("/checkout/cart/buying");
    }, 300);
  };

  const handleContinueBuy = () => {
    sessionStorage.removeItem("added_product");
    let cart_product_array =
      JSON.parse(localStorage.getItem("cart-productArray")) || "";

    if (cart_product_array.length !== 1) {
      sessionStorage.removeItem("active_onlyOneProduct");
    } else {
      sessionStorage.setItem("active_onlyOneProduct", JSON.stringify(true));
    }

    provider.setActivate_loadingPoster(true);

    setTimeout(() => {
      navigate("/checkout/cart/buying");
    }, 300);
  };

  const handleSeeProduct = (product) => {
    sessionStorage.setItem("selectedProduct", JSON.stringify(product));

    setTimeout(() => {
      navigate(`/product-details/${encodeURIComponent(product.title)}`);
    }, 1000);
  };

  return (
    <div
      className="shoppingCart_mainContainer"
      onClick={() => provider.setIsOpen(false)}
    >
      <div className="shoppingCart_content">
        <div className="product_container">
          <span
            className={`title_carrito ${
              cartProductArray.length !== 0 && "paintedCartTitle"
            }`}
          >
            <h5>{`Carrito (${cartProductArray.length})`}</h5>
          </span>
          {cartProductArray.length === 0 ? (
            <div className="warningContainer">
              <div className="image_emptyCart_container">
                <img src={emptyCart} alt="" />
              </div>
              <h4 className="warningText">Tu carrito está vacío</h4>
              <span>
                ¿No sabés qué comprar? ¡Miles de productos te esperan!
              </span>
            </div>
          ) : (
            cartProductArray.map((item) => (
              <div className="productsArray_container" key={item.id}>
                <div className="cart_mainContainer">
                  <div className="picture-title-productsQuantity_container">
                    <div className="picture-title_content">
                      <div
                        className="picture_container"
                        onClick={() => handleSeeProduct(item)}
                      >
                        <img src={item.secure_thumbnail} alt="" />
                      </div>
                      <span
                        className="product-title ml-3"
                        onClick={() => handleSeeProduct(item)}
                      >
                        {item.title}
                      </span>
                    </div>
                  </div>
                  <div className="productsQuantity-totalCost_content">
                    <div className="productsQuantity_container">
                      <div className="buttons-productQuantity_content">
                        <button
                          className={
                            item.products_quantity === 1 ? "disableButton" : ""
                          }
                          onClick={() => handleDiscountProduct(item)}
                        >
                          -
                        </button>
                        <span className="productQuantity">
                          {item.products_quantity}
                        </span>
                        <button
                          className={
                            item.products_quantity === item.available_quantity
                              ? "disableButton"
                              : ""
                          }
                          onClick={() => handleAddProduct(item)}
                        >
                          +
                        </button>
                      </div>
                      <div className="availableQuantity_container">
                        <span className="availableQuantity-text mr-1">
                          {item.available_quantity !== null &&
                            item.available_quantity}
                        </span>
                        {item.available_quantity === null
                          ? "Sin stock"
                          : item.available_quantity === 1
                          ? "disponible"
                          : item.available_quantity > 1 && "disponibles"}
                      </div>
                    </div>
                    <div className="totalCost_container">
                      <div className="discount-originalPrice_container">
                        {item.discount > 0 && (
                          <span className="discount-text">{`${item.discount}% `}</span>
                        )}
                        <span className="originalPrice-text">
                          {item.original_price !== null &&
                            item.conversorMoneda_originalPrice}
                        </span>
                      </div>
                      <span className="product-cost_text">
                        {conversorMoneda(item.totalPrice, item.currency_id)}
                      </span>
                    </div>
                  </div>
                </div>
                <div className="delete-buy_buttons_container">
                  <button
                    className="deleteProduct_button"
                    onClick={() => handleDeleteProduct(item)}
                  >
                    Eliminar
                  </button>
                  <button
                    className="buyNow_button ml-3"
                    onClick={() => handle_buyProduct(item)}
                  >
                    Comprar ahora
                  </button>
                </div>
              </div>
            ))
          )}
          {products_totalCost > 0 && cartProductArray.length > 0 && (
            <section className="finalCostText-compraButton_container">
              <div className="finalCost-text">
                <span className="mr-4">Total</span>
                {conversorMoneda(products_totalCost, provider.product_currency)}
              </div>

              <button className="comprarButton" onClick={handleContinueBuy}>
                Continuar compra
              </button>
            </section>
          )}
        </div>
      </div>
      <Footer />
    </div>
  );
};

export default ShoppingCart;
