import React, { useState } from "react";

import MyProvider from "./context/MyProvider";

// DESKTOP BANNERS
import banner1 from "./assets/banners/banner-1.webp";
import banner2 from "./assets/banners/banner-2.webp";
import banner3 from "./assets/banners/banner-3.webp";
import banner4 from "./assets/banners/banner-4.jpg";
import banner5 from "./assets/banners/banner-5.webp";
import banner6 from "./assets/banners/banner-6.webp";
import banner7 from "./assets/banners/banner-7.webp";

import { BrowserRouter, Routes as Rutas, Route } from "react-router-dom";

// Importo componentes
import Header from "./components/Header";
import Home from "./components/categories/Home";
import Audio_video from "./components/categories/Audio_video";
import Celulares from "./components/categories/Celulares";
import Tecnologia from "./components/categories/Tecnologia";
import Electrodomesticos from "./components/categories/Electrodomesticos";
import Construccion from "./components/categories/Construccion";
import Ropa_accesorios from "./components/categories/Ropa_accesorios";
import Deportes_fitness from "./components/categories/Deportes_fitness";
import SmartTV from "./components/categories_bottom/SmartTV";
import Climatizacion from "./components/categories_bottom/Climatizacion";
import Lavarropas from "./components/categories_bottom/Lavarropas";
import Heladeras from "./components/categories_bottom/Heladeras";
import Notebook from "./components/categories_bottom/Notebook";
import HerramientasElectricas from "./components/categories_bottom/HerramientasElectricas";
import Colchones from "./components/categories_bottom/Colchones";
import ProductOfferArray from "./components/ProductOfferArray";
import FilteredProduct from "./components/FilteredProduct";
import ShoppingCart from "./components/ShoppingCart";
import CheckoutCart from "./components/CheckoutCart";
import Voucher from "./components/Voucher";
import Loading from "./components/Loading";
import ProductDetails from "./components/ProductDetails";

// Importo archivos CSS
import "./css/App.css";
import "./css/Header.css";
import "./css/Home.css";
import "./css/Slider.css";
import "./css/SliderProducts.css";
import "./css/SliderRelatedProducts.css";
import "./css/SliderCategories.css";
import "./css/SliderSmallPictures.css";
import "./css/Product.css";
import "./css/ShoppingCart.css";
import "./css/CheckoutCart.css";
import "./css/Footer.css";
import "./css/Voucher.css";
import "./css/Loading.css";
import "./css/ProductDetails.css";
import "./css/BurgerMenu.css";
import "./css/ProductSlider.css";
import "./css/FilterProducts.css";
import "./css/FilterProductPrices.css";
import "./css/FilterProductCondition.css";
import "./css/FilterProductShipping.css";

//////////////////////////////////////////

function App() {
  const searched_product = sessionStorage.getItem("searchedProduct");
  const selectedProduct =
    JSON.parse(sessionStorage.getItem("selectedProduct")) || "";

  let [searchedProduct, setSearchedProduct] = useState("");
  let [active_loading, setActive_loading] = useState(false);

  let [disable_scroll, setDisable_scroll] = useState(false);

  const handleSearchedProduct = (product) => {
    setSearchedProduct((searchedProduct = product));
  };

  const handleActiveLoading = (e) => {
    setActive_loading((active_loading = e));
  };

  const handleDisableScroll = (e) => {
    setDisable_scroll((disable_scroll = e));
  };

  return (
    <MyProvider className="App_main-container">
      <BrowserRouter basename="/maw-store">
        <div
          className={`App-content ${
            active_loading || (disable_scroll && "disable_scroll")
          }`}
        >
          <Header
            handleSearchedProduct={handleSearchedProduct}
            handleDisableScroll={handleDisableScroll}
          />

          <Rutas>
            <Route path="/">
              <Route path="/" element={<Home />} />
              <Route path="/listado/audio-y-video" element={<Audio_video />} />
              <Route path="/listado/celulares" element={<Celulares />} />
              <Route path="/listado/tecnologia" element={<Tecnologia />} />
              <Route
                path="/listado/electrodomesticos"
                element={<Electrodomesticos />}
              />
              <Route path="/listado/construccion" element={<Construccion />} />
              <Route
                path="/listado/ropa-y-accesorios"
                element={<Ropa_accesorios />}
              />
              <Route
                path="/listado/deportes-y-fitness"
                element={<Deportes_fitness />}
              />
              <Route path="/listado/smart-tv" element={<SmartTV />} />
              <Route
                path="/listado/climatizacion"
                element={<Climatizacion />}
              />
              <Route path="/listado/lavarropas" element={<Lavarropas />} />
              <Route path="/listado/heladeras" element={<Heladeras />} />
              <Route path="/listado/notebook" element={<Notebook />} />
              <Route
                path="/listado/herramientas-electricas"
                element={<HerramientasElectricas />}
              />
              <Route path="/listado/colchones" element={<Colchones />} />
              <Route
                path="/ofertas/:category"
                element={
                  <ProductOfferArray
                    is_arrayParams={[
                      "calefaccion",
                      "electrodomesticos&smart-tv",
                      "tecnologia",
                      "planchita&secador-de-pelo-atma",
                      "sillas-y-muebles",
                      "herramientas",
                      "abrigos",
                    ]}
                    is_array_bannerImages={[
                      banner1,
                      banner2,
                      banner3,
                      banner4,
                      banner5,
                      banner6,
                      banner7,
                    ]}
                  />
                }
              />
              <Route path="/shopping-cart" element={<ShoppingCart />} />
              <Route
                path={"/product-details/:title"}
                element={<ProductDetails />}
              />
              <Route
                path={`/listado/:${searched_product}`}
                element={<FilteredProduct />}
              />
            </Route>
            <Route path="/checkout/cart/buying" element={<CheckoutCart />} />
            <Route path="/checkout/cart/buying/voucher" element={<Voucher />} />
          </Rutas>
        </div>
      </BrowserRouter>
      {active_loading && <Loading />}
    </MyProvider>
  );
}

export default App;
