import React, { useState, useContext } from "react";
import { AppContext } from "../context/MyProvider";
import { useNavigate } from "react-router-dom";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, A11y } from "swiper";

import "swiper/css";

//////// UPLOAD IMAGES
import tv from "../assets/images_categories/smartTV.png";
import celular from "../assets/images_categories/s22.png";
import aireAcondicionado from "../assets/images_categories/aireAcond.png";
import lavarropas from "../assets/images_categories/lavarropas.png";
import heladera from "../assets/images_categories/heladera.png";
import notebook from "../assets/images_categories/notebook.png";
import herramientasElect from "../assets/images_categories/herramientas_elect.webp";
import colchones from "../assets/images_categories/colchones.webp";

const SliderCategories = () => {
  const provider = useContext(AppContext);
  const navigate = useNavigate();

  const [active_shadow, setActive_shadow] = useState(-1);

  const handleRoute = (route) => {
    navigate(route);
  };

  return (
    <>
      <Swiper
        modules={[Navigation, A11y]}
        spaceBetween={29}
        slidesPerView={6}
        navigation={true}
        allowTouchMove={false}
        breakpoints={{
          1441: {
            slidesPerView: 6,
          },

          1440: {
            slidesPerView: 5,
          },

          1300: {
            slidesPerView: 5,
          },

          1200: {
            slidesPerView: 5,
          },

          1100: {
            slidesPerView: 5,
          },

          1081: {
            slidesPerView: 5,
          },

          1025: {
            slidesPerView: 4,
          },
        }}
        className="style-swiper_categories"
      >
        <SwiperSlide className="swipperSlide_content">
          <div className="title-image_container">
            <div
              className={`category_content ${
                active_shadow === 0 && "container_shadow"
              }`}
              onClick={() => {
                provider.setActive_itemNavBar(0);
                handleRoute("/listado/smart-tv");
              }}
            >
              <img src={tv} alt="" />
            </div>
            <span
              onMouseOver={() => setActive_shadow(0)}
              onMouseOut={() => setActive_shadow(-1)}
              onClick={() => handleRoute("/listado/smart-tv")}
            >
              SMART TV
            </span>
          </div>
        </SwiperSlide>
        <SwiperSlide className="swipperSlide_content">
          <div className="title-image_container">
            <div
              className={`category_content ${
                active_shadow === 1 && "container_shadow"
              }`}
              onClick={() => {
                provider.setActive_itemNavBar(0);
                handleRoute("/listado/celulares");
              }}
            >
              <img src={celular} alt="" />
            </div>
            <span
              onMouseOver={() => setActive_shadow(1)}
              onMouseOut={() => setActive_shadow(-1)}
              onClick={() => handleRoute("/listado/celulares")}
            >
              CELULARES
            </span>
          </div>
        </SwiperSlide>
        <SwiperSlide className="swipperSlide_content">
          <div className="title-image_container">
            <div
              className={`category_content ${
                active_shadow === 2 && "container_shadow"
              }`}
              onClick={() => {
                provider.setActive_itemNavBar(0);
                handleRoute("/listado/climatizacion");
              }}
            >
              <img src={aireAcondicionado} alt="" />
            </div>
            <span
              onMouseOver={() => setActive_shadow(2)}
              onMouseOut={() => setActive_shadow(-1)}
              onClick={() => handleRoute("/listado/climatizacion")}
            >
              CLIMATIZACIÓN
            </span>
          </div>
        </SwiperSlide>

        <SwiperSlide className="swipperSlide_content">
          <div className="title-image_container">
            <div
              className={`category_content category_lavarropas ${
                active_shadow === 3 && "container_shadow"
              }`}
              onClick={() => {
                provider.setActive_itemNavBar(0);
                handleRoute("/listado/lavarropas");
              }}
            >
              <img src={lavarropas} alt="" />
            </div>
            <span
              onMouseOver={() => setActive_shadow(3)}
              onMouseOut={() => setActive_shadow(-1)}
              onClick={() => handleRoute("/listado/lavarropas")}
            >
              LAVARROPAS
            </span>
          </div>
        </SwiperSlide>
        <SwiperSlide className="swipperSlide_content">
          <div className="title-image_container">
            <div
              className={`category_content category_heladera ${
                active_shadow === 4 && "container_shadow"
              }`}
              onClick={() => {
                provider.setActive_itemNavBar(0);
                handleRoute("/listado/heladeras");
              }}
            >
              <img src={heladera} alt="" />
            </div>
            <span
              onMouseOver={() => setActive_shadow(4)}
              onMouseOut={() => setActive_shadow(-1)}
              onClick={() => handleRoute("/listado/heladeras")}
            >
              HELADERAS
            </span>
          </div>
        </SwiperSlide>
        <SwiperSlide className="swipperSlide_content">
          <div className="title-image_container">
            <div
              className={`category_content category_notebook ${
                active_shadow === 5 && "container_shadow"
              }`}
              onClick={() => {
                provider.setActive_itemNavBar(0);
                handleRoute("/listado/notebook");
              }}
            >
              <img src={notebook} alt="" />
            </div>
            <span
              onMouseOver={() => setActive_shadow(5)}
              onMouseOut={() => setActive_shadow(-1)}
              onClick={() => handleRoute("/listado/notebook")}
            >
              NOTEBOOK
            </span>
          </div>
        </SwiperSlide>
        <SwiperSlide className="swipperSlide_content">
          <div className="title-image_container">
            <div
              className={`category_content ${
                active_shadow === 6 && "container_shadow"
              }`}
              onClick={() => {
                provider.setActive_itemNavBar(0);
                handleRoute("/listado/herramientas-electricas");
              }}
            >
              <img src={herramientasElect} alt="" />
            </div>
            <span
              onMouseOver={() => setActive_shadow(6)}
              onMouseOut={() => setActive_shadow(-1)}
              onClick={() => handleRoute("/listado/herramientas-electricas")}
            >
              HERRAMIENTAS ELÉCTRICAS
            </span>
          </div>
        </SwiperSlide>
        <SwiperSlide className="swipperSlide_content">
          <div className="title-image_container">
            <div
              className={`category_content ${
                active_shadow === 7 && "container_shadow"
              }`}
              onClick={() => {
                provider.setActive_itemNavBar(0);
                handleRoute("/listado/colchones");
              }}
            >
              <img src={colchones} alt="" />
            </div>
            <span
              onMouseOver={() => setActive_shadow(7)}
              onMouseOut={() => setActive_shadow(-1)}
              onClick={() => handleRoute("/listado/colchones")}
            >
              COLCHONES
            </span>
          </div>
        </SwiperSlide>
      </Swiper>
      {/****** SLIDER VERSION MOBILE ******/}
      <div className="sliderMobile">
        <div className="slides">
          <div className="slide_content">
            <div className="title-image_container">
              <div
                className={`category_content ${
                  active_shadow === 0 && "container_shadow"
                }`}
                onClick={() => {
                  provider.setActive_itemNavBar(0);
                  handleRoute("/listado/smart-tv");
                }}
              >
                <img src={tv} alt="" />
              </div>
              <span onClick={() => handleRoute("/listado/smart-tv")}>
                SMART TV
              </span>
            </div>
          </div>
          <div className="slide_content">
            <div className="title-image_container">
              <div
                className={`category_content ${
                  active_shadow === 1 && "container_shadow"
                }`}
                onClick={() => {
                  provider.setActive_itemNavBar(0);
                  handleRoute("/listado/celulares");
                }}
              >
                <img src={celular} alt="" />
              </div>
              <span onClick={() => handleRoute("/listado/celulares")}>
                CELULARES
              </span>
            </div>
          </div>
          <div className="slide_content">
            <div className="title-image_container">
              <div
                className={`category_content ${
                  active_shadow === 2 && "container_shadow"
                }`}
                onClick={() => {
                  provider.setActive_itemNavBar(0);
                  handleRoute("/listado/climatizacion");
                }}
              >
                <img src={aireAcondicionado} alt="" />
              </div>
              <span onClick={() => handleRoute("/listado/climatizacion")}>
                CLIMATIZACIÓN
              </span>
            </div>
          </div>

          <div className="slide_content">
            <div className="title-image_container">
              <div
                className={`category_content category_lavarropas ${
                  active_shadow === 3 && "container_shadow"
                }`}
                onClick={() => {
                  provider.setActive_itemNavBar(0);
                  handleRoute("/listado/lavarropas");
                }}
              >
                <img src={lavarropas} alt="" />
              </div>
              <span onClick={() => handleRoute("/listado/lavarropas")}>
                LAVARROPAS
              </span>
            </div>
          </div>
          <div className="slide_content">
            <div className="title-image_container">
              <div
                className={`category_content category_heladera ${
                  active_shadow === 4 && "container_shadow"
                }`}
                onClick={() => {
                  provider.setActive_itemNavBar(0);
                  handleRoute("/listado/heladeras");
                }}
              >
                <img src={heladera} alt="" />
              </div>
              <span onClick={() => handleRoute("/listado/heladeras")}>
                HELADERAS
              </span>
            </div>
          </div>
          <div className="slide_content">
            <div className="title-image_container">
              <div
                className={`category_content category_notebook ${
                  active_shadow === 5 && "container_shadow"
                }`}
                onClick={() => {
                  provider.setActive_itemNavBar(0);
                  handleRoute("/listado/notebook");
                }}
              >
                <img src={notebook} alt="" />
              </div>
              <span onClick={() => handleRoute("/listado/notebook")}>
                NOTEBOOK
              </span>
            </div>
          </div>
          <div className="slide_content">
            <div className="title-image_container">
              <div
                className={`category_content ${
                  active_shadow === 6 && "container_shadow"
                }`}
                onClick={() => {
                  provider.setActive_itemNavBar(0);
                  handleRoute("/listado/herramientas-electricas");
                }}
              >
                <img src={herramientasElect} alt="" />
              </div>
              <span
                onClick={() => handleRoute("/listado/herramientas-electricas")}
              >
                HERRAMIENTAS ELÉCTRICAS
              </span>
            </div>
          </div>
          <div className="slide_content">
            <div className="title-image_container">
              <div
                className={`category_content ${
                  active_shadow === 7 && "container_shadow"
                }`}
                onClick={() => {
                  provider.setActive_itemNavBar(0);
                  handleRoute("/listado/colchones");
                }}
              >
                <img src={colchones} alt="" />
              </div>
              <span onClick={() => handleRoute("/listado/colchones")}>
                COLCHONES
              </span>
            </div>
          </div>
        </div>
      </div>
      {/***********************************/}
    </>
  );
};

export default SliderCategories;
