import React, { useState, useContext } from "react";
import { AppContext } from "../context/MyProvider";

const FilterProductPrices = ({ handleFilterProductPrices }) => {
  const provider = useContext(AppContext);

  const [isFilterOn, setIsFilterOn] = useState(false);

  const [currentValue, setCurrentValue] = useState("");

  const options = [
    { value: "Por defecto", label: "Por defecto" },
    { value: "Más vendido", label: "Más vendido" },
    { value: "Menor precio", label: "Menor precio" },
    { value: "Mayor precio", label: "Mayor precio" },
    // Agrega más opciones según tus necesidades
  ];

  const handleActivate_filter_prices = () => {
    provider.setActivate_optionsContainer(!provider.activate_optionsContainer);
  };

  const handle_filterPriceValue = (priceValue) => {
    setCurrentValue(priceValue);
    handleFilterProductPrices(priceValue);
    setIsFilterOn(!false);
    provider.setActivate_optionsContainer(false);
  };

  return (
    <div
      className={`filter-product-prices_container ${
        provider.activate_optionsContainer &&
        "extendHeight_filter-product-prices_container"
      }`}
      onClick={handleActivate_filter_prices}
    >
      <span className="order-text">Ordenar por</span>
      <div className="current-value" onClick={handleActivate_filter_prices}>
        <span>{isFilterOn ? currentValue : options[0].label}</span>
      </div>

      <div className="valuesFilter-arrowButton_container">
        <div
          className={`filterValues_container ${
            provider.activate_optionsContainer && "extend_filterPricesContainer"
          }`}
        >
          {options.map((item) => (
            <span
              key={item.value}
              value={item.value}
              onClick={() => {
                handle_filterPriceValue(item.value);
                provider.setIsActiveFilterMode(false);
                provider.setHideProductContainer(false);
              }}
              className={`option-value ${
                currentValue === item.label && "paintedValue"
              }`}
            >
              {item.label}
            </span>
          ))}
        </div>
        <button
          className={`arrow_button ${
            provider.activate_optionsContainer && "rotateArrow"
          }`}
        >
          <i className="fa-solid fa-chevron-down custom-arrowDown"></i>
        </button>
      </div>

      <button
        className={`arrow_button_mobile ${
          provider.activate_optionsContainer && "rotateArrow"
        }`}
      >
        <i className="fa-solid fa-chevron-down custom-arrowDown-mobile"></i>
      </button>
    </div>
  );
};

export default FilterProductPrices;
