import React, { useState, useEffect, useContext } from "react";
import { AppContext } from "../context/MyProvider";
import { useNavigate } from "react-router-dom";

import unpainted_cart from "../assets/carts/cart_1a.png";
import painted_cart from "../assets/carts/cart_1b.png";

const Product = ({
  productData_array,
  is_productWithoutOffer,
  product_without_offer,
  is_productWithOffer,
  product_with_offer,
  is_sliderProducts,
  slider_product,
  is_sliderRelatedProducts,
  slider_relatedProduct,
  function_conversorMoneda,
  function_calculateDiscountPercentage,
  useSwiperSlide,
}) => {
  const provider = useContext(AppContext);
  const navigate = useNavigate();
  const [loadedProductDataArray, setLoadedProductDataArray] = useState(false);
  const [product, setProduct] = useState({});
  const [activeCartIcon, setActiveCartIcon] = useState(false);
  const [activeEffectCartIcon, setActiveEffectCartIcon] = useState(false);
  let [activeProductCart, setActiveProductCart] = useState(false);
  const [active_loadingCheckCart, setActive_loadingCheckCart] = useState(false);
  const [show_checkIcon, setShow_checkIcon] = useState(false);
  const [product_installments, setProduct_installments] = useState("");
  const [valueVerticalBar, setValueVerticalBar] = useState("");

  useEffect(() => {
    const tempLoadedProductDataArray = true;
    setLoadedProductDataArray(tempLoadedProductDataArray);
  }, []);

  useEffect(() => {
    if (is_productWithoutOffer === "producto_sin_oferta") {
      setProduct(product_without_offer);
    }

    if (is_productWithOffer === "producto_con_oferta") {
      setProduct(product_with_offer);
    }

    if (is_sliderProducts === "slider_productos") {
      setProduct(slider_product);
    }

    if (is_sliderRelatedProducts === "slider_productos_relatedProducts") {
      setProduct(slider_relatedProduct);
    }
  }, []);

  useEffect(() => {
    productData_array.forEach((item) => {
      if (product_with_offer && product_with_offer.id === item.id) {
        setProduct_installments(item.installments);
      }

      if (product_without_offer && product_without_offer.id === item.id) {
        setProduct_installments(item.installments);
      }
    });
  }, [productData_array, product_with_offer, product_without_offer]);

  const handle_selectedProduct = () => {
    const article_randomNumber = Math.floor(Math.random() * 10000) + 1;

    product.articleRandomNumber = article_randomNumber;

    sessionStorage.setItem("selectedProduct", JSON.stringify(product));

    setTimeout(() => {
      navigate(`/product-details/${encodeURIComponent(product.title)}`);
    }, 1000);
  };

  const handleCartIcon = (event) => {
    event.stopPropagation();

    setActive_loadingCheckCart(true);

    setTimeout(() => {
      setActive_loadingCheckCart(false);
      setShow_checkIcon(true);
    }, 700);

    setActiveEffectCartIcon(!activeEffectCartIcon);
    const cartProduct = {
      id: product.id,
      title: product.title,
      available_quantity: product.available_quantity,
      products_quantity: 1,
      category_id: product.category_id,
      original_price: product.original_price,
      price: product.price,
      totalPrice: product.price,
      currency_id: product.currency_id,
      accepts_mercadopago: product.accepts_mercadopago,
      attributes: product.attributes,
      condition: product.condition,
      pictures: product.pictures,
      sale_terms: product.sale_terms,
      secure_thumbnail: product.secure_thumbnail,
      shipping: product.shipping,
      sold_quantity: product.sold_quantity,
      warranty: product.warranty,
      discount: function_calculateDiscountPercentage(
        product.original_price,
        product.price
      ),
      conversorMoneda_originalPrice: function_conversorMoneda(
        product.original_price,
        product.currency_id
      ),
      conversorMoneda_price: function_conversorMoneda(
        product.price,
        product.currency_id
      ),
    };

    provider.setProduct_currency(product.currency_id);

    if (Array.isArray(provider.array_productsCart)) {
      setActiveProductCart((activeProductCart = !activeProductCart));
      if (activeProductCart) {
        const updatedArray = [...provider.array_productsCart];
        const index = updatedArray.findIndex((item) => item.id === product.id);

        if (index === -1) {
          updatedArray.push(cartProduct);
        } else {
          updatedArray[index].products_quantity++;
        }

        localStorage.setItem("cart-productArray", JSON.stringify(updatedArray));

        provider.setArray_productsCart(updatedArray);
      } else {
        const filterProduct = provider.array_productsCart.filter(
          (item) => item.id !== product.id
        );

        localStorage.setItem(
          "cart-productArray",
          JSON.stringify(filterProduct)
        );

        provider.setArray_productsCart(filterProduct);
      }
    } else {
      console.log("provider.array_productsCart is not an array");
    }
  };

  useEffect(() => {
    const verticalBar = " | ";

    setValueVerticalBar(verticalBar);
  }, []);

  const ContainerComponent = useSwiperSlide ? React.Fragment : "div";

  return (
    <ContainerComponent key={product.id}>
      {product && (
        <div
          onMouseOver={() => setActiveCartIcon(true)}
          onMouseOut={() => setActiveCartIcon(false)}
          style={{ position: "relative", cursor: "pointer" }}
          onClick={handle_selectedProduct}
          className="product-card_container"
        >
          {activeCartIcon && (
            <div
              className="cartIcon-container"
              onClick={(event) => handleCartIcon(event)}
            >
              <img
                className="custom-cartIcon"
                src={
                  activeEffectCartIcon || provider.array_productsCart > 0
                    ? painted_cart
                    : unpainted_cart
                }
                alt=""
              />
            </div>
          )}

          <section className="img-titleProduct_container_product">
            <div className="image-container_product">
              {product.pictures && product.pictures.length >= 0 && (
                <img
                  src={product.pictures[0].secure_url}
                  alt=""
                  loading="lazy"
                />
              )}

              {/*** CART ICON VERSION MOBILE ***/}
              {loadedProductDataArray && (
                <div
                  className="cartIcon-container_mobileButton"
                  onClick={(event) => handleCartIcon(event)}
                >
                  {
                    <div className="d-flex justify-content-center">
                      <img
                        className="custom-cartIcon"
                        src={
                          activeEffectCartIcon ||
                          provider.array_productsCart > 0
                            ? painted_cart
                            : unpainted_cart
                        }
                        alt=""
                      />
                    </div>
                  }
                </div>
              )}
              {/**********************/}
            </div>
          </section>
          <section className="price-installments-shipping__container_product">
            <div className="title-product_container">
              <h5 className="title-product">{product.title}</h5>
            </div>
            <div className="price-installments-shipping_content">
              <div className="prices-discount_content_product">
                <div
                  className={`original_price-text_product ${
                    product.original_price !== null &&
                    "marginRight_originalPrice"
                  }`}
                >
                  <span>
                    {product.original_price !== null
                      ? function_conversorMoneda(
                          product.original_price,
                          product.currency_id
                        )
                      : ""}
                  </span>
                </div>
                <span
                  className={`discount_price-text_product ${
                    product.original_price === null && "remove_marginLeft"
                  }`}
                >
                  {function_conversorMoneda(product.price, product.currency_id)}
                </span>
              </div>
              {product.original_price !== null &&
                product.price !== null &&
                function_calculateDiscountPercentage(
                  product.original_price,
                  product.price
                ) > 0 && (
                  <div className="discount">
                    <b>
                      <span>
                        {function_calculateDiscountPercentage(
                          product.original_price,
                          product.price
                        )}
                      </span>
                      <span>%</span>
                    </b>
                    <span className="ml-1">OFF</span>
                  </div>
                )}
            </div>
            {product_installments &&
            product_installments.installments !== "" &&
            product_installments.rate === 0 ? (
              <div className="installments_container">
                <span className="productQuantity-text">{`${product_installments.quantity} `}</span>
                <span>x</span>
                <span className="productAmount-text">{` ${function_conversorMoneda(
                  product_installments.amount,
                  product.currency_id
                )} `}</span>
                <span>Sin Interés</span>
              </div>
            ) : (
              ""
            )}
            <div className="condition-soldQuantity-product">
              <span>
                {product.condition === "new" && "Nuevo"}

                {product.condition === "used" && "Usado"}
                {product.sold_quantity !== 0 && valueVerticalBar}
                {product.sold_quantity !== 0 && product.sold_quantity}
                {product.sold_quantity === 1
                  ? " vendido"
                  : product.sold_quantity > 1 && " +vendidos"}
              </span>
            </div>

            <div className="shipping_content_product">
              {product.shipping?.free_shipping && (
                <div className="icon-text_shipping_container_product">
                  <span className="shipping-text_product">
                    <span style={{ fontSize: 12 }}>AMBA</span>, Envío Gratis y
                    en 24hs
                  </span>

                  <i className="fa-solid fa-truck icon-shipping_product"></i>
                </div>
              )}
            </div>
          </section>
        </div>
      )}
    </ContainerComponent>
  );
};

export default Product;
