import React, { useState, useEffect, useContext } from "react";
import { AppContext } from "../context/MyProvider";

const FilterProductShipping = ({ handleFilterProductShipping }) => {
  const provider = useContext(AppContext);

  let [isFilterOn, setIsFilterOn] = useState(false);

  useEffect(() => {
    const is_filter_on = JSON.parse(sessionStorage.getItem("isFilterOn")) || "";

    if (is_filter_on) {
      setIsFilterOn(true);

      setTimeout(() => {
        const freeShipping = "envío gratis";

        handleFilterProductShipping(freeShipping);
      }, 100);
    }
  }, []);

  const handleToggleFilter = () => {
    setIsFilterOn((isFilterOn = !isFilterOn));
    sessionStorage.setItem("isFilterOn", JSON.stringify(isFilterOn));
    setIsFilterOn(JSON.parse(sessionStorage.getItem("isFilterOn")) || "");
    if (isFilterOn) {
      setTimeout(() => {
        const freeShipping = "envío gratis";

        handleFilterProductShipping(freeShipping);
      }, 800);

      setTimeout(() => {
        provider.setIsActiveFilterMode(false);
        provider.setHideProductContainer(false);
      }, 1000);
    } else {
      setTimeout(() => {
        const shippingPerDefault = "envío por defecto";
        handleFilterProductShipping(shippingPerDefault);
      }, 800);
    }
  };

  return (
    <div
      className="filterProductShipping_container"
      onClick={handleToggleFilter}
    >
      <span className="freeShipping-text">Envío gratis</span>
      <div
        className={`switch-button_container ${
          isFilterOn && "change_backgroundColor"
        }`}
      >
        <button
          className={`switch-roundedIcon ${isFilterOn && "activate_switch"}`}
        ></button>
      </div>
    </div>
  );
};

export default FilterProductShipping;
