import React, { useState, useEffect, useContext } from "react";
import { useNavigate } from "react-router-dom";
import { AppContext } from "../context/MyProvider";
import BuyProductDetails from "./BuyProductDetails";

import mawstoreIcon from "../assets/mawstore-logo.png";
import visaIcon from "../assets/card_icons/visa.png";
import mastercardIcon from "../assets/card_icons/mastercard-2.png";
import cabalIcon from "../assets/card_icons/cabal.png";
import amerExpressIcon from "../assets/card_icons/amer-express.png";

const CheckoutCart = () => {
  const navigate = useNavigate();
  const provider = useContext(AppContext);

  const { setActive_checkoutCart, conversorMoneda, product_currency } =
    useContext(AppContext);
  const cartProductArray =
    JSON.parse(localStorage.getItem("cart-productArray")) || [];

  const selectedProduct =
    JSON.parse(sessionStorage.getItem("selectedProduct")) || "";

  const active_onlyOneProduct =
    JSON.parse(sessionStorage.getItem("active_onlyOneProduct")) || "";

  let [product_quantity, setProduct_quantity] = useState(0);
  let [totalCost, setTotalCost] = useState(0);
  let [onlyOneProduct, setOnlyOneProduct] = useState({});
  const [productArray, setProductArray] = useState([]);
  const [cardIcon, setCardIcon] = useState("");
  const [cardNumber, setCardNumber] = useState("");
  const [customerFullName, setCustomerFullName] = useState("");
  const [dueDate, setDueDate] = useState("");
  const [securityCode, setSecurityCode] = useState("");
  const [customerDNI, setCustomerDNI] = useState("");
  const [pressBuyButton, setPressBuyButton] = useState(false);
  const [activateSuccessfullyEffect, setActivateSuccessfullyEffect] =
    useState(false);

  useEffect(() => {
    setTimeout(() => {
      provider.setActivate_loadingPoster(false);
    }, 2000);
  }, []);

  useEffect(() => {
    setActive_checkoutCart(true);

    let tempQuantity = 0;
    let tempTotalCost = 0;
    const tempProductArrayAdded = [];

    cartProductArray.forEach((item) => {
      tempProductArrayAdded.push(item);

      tempQuantity += 1;
      tempTotalCost += item.totalPrice;
    });

    setProductArray(tempProductArrayAdded);
    setProduct_quantity(tempQuantity);
    setTotalCost(tempTotalCost);
  }, [totalCost, product_quantity]);

  useEffect(() => {
    let tempOnlyOneProduct =
      JSON.parse(sessionStorage.getItem("added_product")) || "";

    setOnlyOneProduct((onlyOneProduct = tempOnlyOneProduct));
  }, []);

  const handleCustomerFullNameInput = (e) => {
    const inputValue = e.target.value.toUpperCase();
    const onlyLetters = /^[a-zA-Z\s]*$/; // Expresión regular para permitir solo letras y espacios

    if (onlyLetters.test(inputValue) || inputValue === "") {
      setCustomerFullName(inputValue);
    }
  };

  const handleKeyDown_cardNumber = (event) => {
    const allowedKeys = [
      "Backspace",
      "ArrowLeft",
      "ArrowRight",
      "Delete",
      "Tab",
    ];
    const isNumericKey = /^[0-9]$/.test(event.key);
    const currentValue = event.target.value;
    const formattedValue = currentValue.replace(/\s/g, ""); // Eliminar espacios existentes
    const firstCharacter = formattedValue.charAt(0);
    const maxLength = 19;

    if (!isNumericKey && !allowedKeys.includes(event.key)) {
      event.preventDefault();
    }

    if (formattedValue.length === 0) {
      setCardIcon("");
    } else if (/^[25]$/.test(firstCharacter)) {
      setCardIcon(mastercardIcon);
    } else if (firstCharacter === "4") {
      setCardIcon(visaIcon);
    } else if (firstCharacter === "6") {
      setCardIcon(cabalIcon);
    } else if (firstCharacter === "3") {
      setCardIcon(amerExpressIcon);
    } else {
      setCardIcon("");
    }

    if (
      event.nativeEvent.inputType === "deleteContentBackward" &&
      formattedValue.length === maxLength - 1
    ) {
      setCardIcon(""); // Limpiar el ícono al borrar el último carácter
    }

    if (formattedValue.length <= maxLength) {
      const formattedNumber = formattedValue
        .replace(/\D/g, "")
        .replace(/(\d{4})(?=\d)/g, "$1 ");

      event.target.value = formattedNumber;
    }
  };

  const handleKeyDown_dueDate = (event) => {
    const allowedKeys = [
      "Backspace",
      "ArrowLeft",
      "ArrowRight",
      "Delete",
      "/",
      "Tab",
    ];
    const currentValue = event.target.value;
    const caretPosition = event.target.selectionStart;
    const isNumericKey = /^\d$/.test(event.key);
    const hasReachedMaxLength = currentValue.length >= 7;
    const firstTwoDigits = currentValue.slice(0, 2);
    const isInvalidMonth = parseInt(firstTwoDigits) > 12;
    const isLetterKey = /^[a-zA-Z]$/.test(event.key);

    if (
      caretPosition < 2 ||
      (caretPosition === 2 && event.key === "/") ||
      (caretPosition > 2 && isNumericKey && !hasReachedMaxLength) ||
      allowedKeys.includes(event.key)
    ) {
      if ((caretPosition === 0 || caretPosition === 1) && isLetterKey) {
        event.preventDefault();
      }

      if (
        caretPosition === 1 &&
        isNumericKey &&
        parseInt(currentValue + event.key) > 12
      ) {
        event.preventDefault();
      }

      return;
    }

    event.preventDefault();
  };

  const handleKeyDown_securityCode = (event) => {
    const allowedKeys = [
      "Backspace",
      "ArrowLeft",
      "ArrowRight",
      "Delete",
      "Tab",
    ];
    const isNumericKey = /^[0-9]$/.test(event.key);
    const hasReachedMaxLength = event.target.value.length >= 3;

    if (!isNumericKey && !allowedKeys.includes(event.key)) {
      event.preventDefault();
    }

    if (isNumericKey && hasReachedMaxLength) {
      event.preventDefault();
    }
  };

  const handleKeyDown_DNI = (event) => {
    const allowedKeys = [
      "Backspace",
      "ArrowLeft",
      "ArrowRight",
      "Delete",
      "Tab",
    ];
    const isNumericKey = /^\d$/.test(event.key);

    if (!isNumericKey && !allowedKeys.includes(event.key)) {
      event.preventDefault();
    }
  };

  const handleBlur_DNI = (event) => {
    const currentValue = event.target.value.replace(/\s/g, ""); // Eliminar espacios existentes
    const formattedValue = currentValue.replace(/\B(?=(\d{3})+(?!\d))/g, "."); // Agregar puntos de separación

    event.target.value = formattedValue;

    const hasReachedMaxLength = event.target.value.length >= 8;
    const hasInvalidCharacters = /\D/.test(event.target.value); // Verificar si hay caracteres no numéricos

    if (hasReachedMaxLength || hasInvalidCharacters) {
      event.preventDefault();
    }
  };

  const handle_customerPayment = () => {
    if (!cardNumber.trim()) {
      console.log(`/**** *** *** cardNumber: `, cardNumber);
      return;
    }

    if (!customerFullName.trim()) {
      console.log(`/**** *** *** customerFullName: `, customerFullName);
      return;
    }

    if (!dueDate.trim()) {
      console.log(`/**** *** *** dueDate: `, dueDate);
      return;
    }

    if (!securityCode.trim()) {
      console.log(`/**** *** *** securityCode: `, securityCode);
      return;
    }

    if (!customerDNI.trim()) {
      console.log(`/**** *** *** customerDNI: `, customerDNI);
      return;
    }

    const words = customerFullName.trim().split(" ");

    if (
      cardNumber.length === 19 &&
      dueDate.length === 7 &&
      securityCode.length === 3 &&
      customerDNI.length === 8 &&
      words.length >= 2 &&
      words.length <= 3
    ) {
      setActivateSuccessfullyEffect(true);

      setTimeout(() => {
        setPressBuyButton(true);
      }, 1400);

      setTimeout(() => {
        sessionStorage.setItem("blockBackToHome", JSON.stringify(true));

        sessionStorage.setItem(
          "onlyOneProduct",
          JSON.stringify(onlyOneProduct)
        );

        let customerObject = {};

        sessionStorage.setItem("productArray", JSON.stringify(productArray));

        const currentDate = new Date().toLocaleDateString();
        const randomNumber = Math.floor(Math.random() * 10000) + 1;

        customerObject.customerFullName = customerFullName;
        customerObject.totalCostBuy = totalCost;
        customerObject.currentDate = currentDate;
        customerObject.randomNumber = randomNumber;

        sessionStorage.setItem(
          "customerObject",
          JSON.stringify(customerObject)
        );

        navigate("/checkout/cart/buying/voucher");
      }, 2400);
    }
  };

  const handleReturnHome = (route) => {
    navigate(route);
  };

  return (
    <>
      {provider.activate_loadingPoster ? (
        <div className="loadingPoster_container">
          <div className="d-flex justify-content-center loading-content">
            <div className="spinner-border custom-loadingSpinner" role="status">
              <span className="sr-only">Loading...</span>
            </div>
          </div>
          <span>Preparando todo para tu compra.</span>
        </div>
      ) : (
        <div className="checkout_container">
          <div className="mawstoreLogo_container">
            <div
              className="custom-logo-container"
              onClick={() => handleReturnHome("/")}
            >
              <img className="mawStore-logo" src={mawstoreIcon} alt="" />
            </div>
          </div>

          <div className="bottomContainer_checkout">
            <section className="paymentForm_container">
              <h5 className="paymentForm-title">Información de la tarjeta</h5>
              <form>
                <div className="labelInput_container">
                  <label htmlFor="cardNumberInput">Número de tarjeta</label>
                  <input
                    type="text"
                    id="cardNumberInput"
                    onInput={handleKeyDown_cardNumber}
                    maxLength={19}
                    onChange={(e) => setCardNumber(e.target.value)}
                    value={cardNumber}
                  />
                  {cardIcon && (
                    <div className="cardIcon_container">
                      <img src={cardIcon} alt="" />
                    </div>
                  )}
                </div>

                <div className="labelInput_container">
                  <label htmlFor="nameInput">Nombre y apellido</label>
                  <input
                    type="text"
                    id="nameInput"
                    onChange={handleCustomerFullNameInput}
                    value={customerFullName}
                  />
                  <span className="indication-text">
                    Tal cual esté impreso en la tarjeta.
                  </span>
                </div>

                <div className="card_date-code_container">
                  <div className="labelInput_container dueDate_container">
                    <label htmlFor="dueDateInput">Fecha de vencimiento</label>
                    <input
                      type="text"
                      id="dueDateInput"
                      onKeyDown={handleKeyDown_dueDate}
                      maxLength={7}
                      onChange={(e) => setDueDate(e.target.value)}
                      value={dueDate}
                    />
                    <span className="indication-text">Mes / Año</span>
                  </div>

                  <div className="labelInput_container securityCode_container">
                    <label htmlFor="securityCodeInput">
                      Código de seguridad
                    </label>
                    <input
                      type="text"
                      id="securityCodeInput"
                      onKeyDown={handleKeyDown_securityCode}
                      maxLength={3}
                      onChange={(e) => setSecurityCode(e.target.value)}
                      value={securityCode}
                    />
                    <span className="indication-text">
                      Los 3 números del dorso de tu tarjeta.
                    </span>
                  </div>
                </div>
                <div className="labelInput_container">
                  <label htmlFor="DNIinput">
                    DNI del titular de la tarjeta
                  </label>
                  <input
                    type="text"
                    id="DNIinput"
                    onKeyDown={handleKeyDown_DNI}
                    onBlur={handleBlur_DNI}
                    maxLength={8}
                    onChange={(e) => setCustomerDNI(e.target.value)}
                    value={customerDNI}
                  />
                </div>
              </form>
            </section>

            <section className="resumenCompra_container">
              <div className="top-container">
                {selectedProduct ? (
                  <BuyProductDetails
                    is_selectedProduct={"is_selectedProduct"}
                    selectedProduct={selectedProduct}
                  />
                ) : productArray.length === 1 && onlyOneProduct === "" ? (
                  productArray.map((item) => (
                    <BuyProductDetails
                      is_productArray={"is_productArray"}
                      item={item}
                      key={item.id}
                    />
                  ))
                ) : onlyOneProduct !== "" ? (
                  <BuyProductDetails
                    is_onlyOneProduct={"is_onlyOneProduct"}
                    onlyOneProduct={onlyOneProduct}
                  />
                ) : (
                  <h5>Resumen de compra</h5>
                )}

                <div className="product-quantity_container">
                  {
                    <span className="productsQuantity">
                      {productArray.length !== 1 &&
                        onlyOneProduct === "" &&
                        !selectedProduct &&
                        `Productos (${product_quantity})`}
                      {active_onlyOneProduct ||
                      (selectedProduct &&
                        selectedProduct.products_quantity === 1)
                        ? "Producto"
                        : ""}
                    </span>
                  }
                  <span className="products-totalPrice">
                    {selectedProduct
                      ? conversorMoneda(selectedProduct.price, product_currency)
                      : (productArray.length !== 1 && onlyOneProduct === "") ||
                        productArray.length === 1
                      ? conversorMoneda(totalCost, product_currency)
                      : conversorMoneda(
                          onlyOneProduct.totalPrice,
                          product_currency
                        )}
                  </span>
                </div>
                <div className="total-cost_container">
                  <span>Total</span>
                  <span className="totalAmoun-text">
                    {selectedProduct
                      ? conversorMoneda(selectedProduct.price, product_currency)
                      : (productArray.length !== 1 && onlyOneProduct === "") ||
                        productArray.length === 1
                      ? conversorMoneda(totalCost, product_currency)
                      : conversorMoneda(
                          onlyOneProduct.totalPrice,
                          product_currency
                        )}
                  </span>
                </div>
              </div>

              <button
                type="submit"
                className={`buttonBuy_container ${
                  activateSuccessfullyEffect && "active_successfullyEffect"
                }`}
                onClick={handle_customerPayment}
              >
                {pressBuyButton ? (
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="23"
                    height="23"
                    fill="currentColor"
                    className="bi bi-check-circle-fill show_checkIcon"
                    viewBox="0 0 16 16"
                  >
                    <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zm-3.97-3.03a.75.75 0 0 0-1.08.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-.01-1.05z" />
                  </svg>
                ) : (
                  "Comprar"
                )}
              </button>
            </section>
          </div>
        </div>
      )}
    </>
  );
};

export default CheckoutCart;
