import React, { useEffect, useState, useContext } from "react";
import { AppContext } from "../context/MyProvider";

import { useParams, useNavigate } from "react-router-dom";
import ReactImageMagnify from "react-image-magnify";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination, A11y } from "swiper";
import "swiper/swiper-bundle.min.css";
import "swiper/swiper.min.css";
import Loading from "./Loading";
import Footer from "../components/Footer";
import SliderRelatedProducts from "../components/SliderRelatedProducts";

import visa_icon from "../assets/card_icons/visa.png";
import mastercard_icon from "../assets/card_icons/mastercard-2.png";
import cabal_icon from "../assets/card_icons/cabal.png";
import amerExpress_icon from "../assets/card_icons/amer-express.png";
import ahora3 from "../assets/NOW-icons/ahora-3.jpg";
import ahora6 from "../assets/NOW-icons/ahora-6.jpg";
import ahora12 from "../assets/NOW-icons/ahora-12.jpg";

const ProductDetails = () => {
  const { conversorMoneda, calculateDiscountPercentage } =
    useContext(AppContext);
  const navigate = useNavigate();
  const provider = useContext(AppContext);

  const { title } = useParams();
  const [selectedProduct, setSelectedProduct] = useState("");
  const [picturesArray, setPicturesArray] = useState("");
  const [categoryPath, setCategoryPath] = useState("");
  const [selected_image, setSelected_image] = useState("");
  const [selected_imageID, setSelected_imageID] = useState("");
  const [activate_imageChange, setActivate_imageChange] = useState(false);
  const [activate_changeBorderColor, setActivate_changeBorderColor] =
    useState(false);

  const [allProductDetails, setAllProductDetails] = useState("");
  const [nowIcon, setNowIcon] = useState("");

  const [first_detailsContainer, setFirst_detailsContainer] = useState("");

  const [second_detailsContainer, setSecond_detailsContainer] = useState("");

  useEffect(() => {
    const tempSelectedProduct =
      JSON.parse(sessionStorage.getItem("selectedProduct")) || "";

    let tempPicturesArray = [];

    for (let i = 0; i < tempSelectedProduct.pictures.length; i++) {
      if (i < 20) {
        tempPicturesArray.push(tempSelectedProduct.pictures[i]);
      }
    }

    let temp_firstAttributesArray = [];
    let temp_secondAttributesArray = [];
    for (let i = 0; i < tempSelectedProduct.attributes.length; i++) {
      if (tempSelectedProduct.attributes.length > 30) {
        if (i < 15) {
          temp_firstAttributesArray.push(tempSelectedProduct.attributes[i]);
        } else if (i > 15 && i <= 30) {
          temp_secondAttributesArray.push(tempSelectedProduct.attributes[i]);
        }
      } else {
        if (i < tempSelectedProduct.attributes.length / 2) {
          temp_firstAttributesArray.push(tempSelectedProduct.attributes[i]);
        } else {
          temp_secondAttributesArray.push(tempSelectedProduct.attributes[i]);
        }
      }
    }
    setFirst_detailsContainer(temp_firstAttributesArray);

    setSecond_detailsContainer(temp_secondAttributesArray);

    setPicturesArray(tempPicturesArray);

    setSelectedProduct(tempSelectedProduct);

    const getProductCategories = async () => {
      try {
        const res = await fetch(
          `https://api.mercadolibre.com/categories/${tempSelectedProduct.category_id}`
        );
        const data = await res.json();

        setCategoryPath(data.path_from_root);
      } catch (error) {
        console.log(error);
      }
    };

    getProductCategories();

    const get_allProductDetails = async () => {
      try {
        const res = await fetch(
          `https://api.mercadolibre.com/sites/MLA/search?q=${tempSelectedProduct.title}`
        );

        const data = await res.json();

        const findedProduct = data.results.find(
          (product) =>
            product.id === tempSelectedProduct.id ||
            product.id !== tempSelectedProduct.id
        );

        setAllProductDetails(findedProduct);

        const findedProduct_nowIcon =
          findedProduct &&
          findedProduct.tags &&
          findedProduct.tags.find(
            (item) =>
              item === "ahora-3" ||
              item === "mshops_ahora-3" ||
              item === "ahora-6" ||
              item === "mshops_ahora-6" ||
              item === "ahora-12" ||
              "mshops_ahora-12"
          );

        setNowIcon(findedProduct_nowIcon);
      } catch (error) {
        console.log(error);
      }
    };
    get_allProductDetails();
  }, [title]);

  const categoryPathString = categoryPath
    ? categoryPath.map((item) => item.name).join(" > ")
    : "";

  const handle_imageSelector = (image) => {
    setSelected_image(image);
    setActivate_imageChange(true);

    const filtered_image = picturesArray.filter(
      (item) => item.secure_url === image.secure_url
    );

    if (filtered_image[0].id === image.id) {
      setSelected_imageID(image.id);
      setActivate_changeBorderColor(true);
    }
  };

  const handle_continueBuy = () => {
    provider.setActivate_loadingPoster(true);
    selectedProduct.products_quantity = 1;

    setTimeout(() => {
      sessionStorage.setItem(
        "selectedProduct",
        JSON.stringify(selectedProduct)
      );

      navigate("/checkout/cart/buying");
    }, 300);
  };

  return (
    <>
      {categoryPath && allProductDetails ? (
        <div
          id="productDetailsContainer"
          className="productDetails_container"
          onClick={() => provider.setIsOpen(false)}
        >
          <section className="productInfo_container">
            <div className="categoriesDetails_container">
              <span>{categoryPath.length > 0 ? "Inicio > " : ""}</span>
              <span>{categoryPathString}</span>
            </div>

            <div className="productContainer">
              <div className="product_content">
                <div className="images_container">
                  <div className="fluid">
                    <div className="fluid__image-container image-preview_container">
                      <ReactImageMagnify
                        imageClassName="image-preview"
                        enlargedImageContainerClassName="zoomed-image"
                        {...{
                          smallImage: {
                            alt: "",
                            src: activate_imageChange
                              ? selected_image.secure_url
                              : picturesArray[0].secure_url,
                            width: 400,
                            height: 400,
                          },
                          largeImage: {
                            src: activate_imageChange
                              ? selected_image.secure_url
                              : picturesArray[0].secure_url,
                            width: 900, // Ancho fijo del visor de zoom
                            height: 900, // Alto fijo del visor de zoom
                          },
                          enlargedImageContainerDimensions: {
                            width: "100%",
                            height: "100%",
                          },
                          enlargedImagePosition: "over", // Posicionamiento del visor de zoom sobre la imagen
                          enlargedImageStyle: {
                            objectFit: "contain", // Ajuste de la imagen para mantener sus proporciones
                            objectPosition: "center", // Posición central de la imagen dentro del visor de zoom
                          },
                        }}
                      />
                    </div>
                  </div>

                  <Swiper
                    modules={[Navigation, Pagination, A11y]}
                    spaceBetween={16}
                    slidesPerView={5}
                    navigation={true}
                    allowTouchMove={false}
                    pagination={{ clickable: true }}
                    breakpoints={{
                      1440: {
                        slidesPerView: 5,
                      },
                      1300: {
                        slidesPerView: 5,
                      },
                      1200: {
                        slidesPerView: 4,
                      },

                      1100: {
                        slidesPerView: 4,
                      },
                    }}
                    className={`images-list_container ${
                      picturesArray && picturesArray.length <= 3
                        ? "swiper-wrapper"
                        : ""
                    }`}
                  >
                    {picturesArray &&
                      picturesArray.map((item) => (
                        <>
                          <SwiperSlide
                            className={`smallPicture_container ${
                              (item.id === picturesArray[0].id &&
                                !activate_changeBorderColor) ||
                              selected_imageID === item.id
                                ? "changeBorderColor_imageContainer"
                                : ""
                            }`}
                            onClick={() => handle_imageSelector(item)}
                            key={item.id}
                          >
                            <img
                              style={{ margin: 20 }}
                              src={item.secure_url}
                              alt=""
                            />
                          </SwiperSlide>
                        </>
                      ))}
                  </Swiper>
                  {/******** SLIDER VERSION MOBILE ********/}
                  <div className="sliderSmallPicture_container">
                    <div
                      className={`slidesSmallPictures_mobile ${
                        picturesArray &&
                        picturesArray.length <= 3 &&
                        "centerContentImages"
                      }`}
                    >
                      {picturesArray &&
                        picturesArray.map((item) => (
                          <div
                            className={`slideSmallPicture_content_mobile ${
                              (item.id === picturesArray[0].id &&
                                !activate_changeBorderColor) ||
                              selected_imageID === item.id
                                ? "changeBorderColor_mobileContainer"
                                : ""
                            }`}
                            onClick={() => handle_imageSelector(item)}
                            key={item.id}
                          >
                            <img
                              style={{ margin: 20 }}
                              src={item.secure_url}
                              alt=""
                            />
                          </div>
                        ))}
                    </div>
                  </div>
                  {/***************************************/}
                </div>
                <div className="buyInfo_container">
                  <span className="condition-soldQuantity_texts">
                    {selectedProduct.condition === "new" && "Nuevo"}

                    {selectedProduct.condition === "used" && "Usado"}
                    {selectedProduct.sold_quantity !== 0 && " | "}
                    {selectedProduct.sold_quantity > 1
                      ? `+${selectedProduct.sold_quantity}`
                      : selectedProduct.sold_quantity === 1 &&
                        selectedProduct.sold_quantity}
                    {selectedProduct.sold_quantity === 1
                      ? " vendido"
                      : selectedProduct.sold_quantity > 1 && " vendidos"}
                  </span>

                  <h5 className="productTitle">{selectedProduct.title}</h5>

                  <div className="originalPrice-priceWidthDiscount-discountPercentage_container">
                    <div className="originalPrice-PriceWithDiscount_container">
                      <span className="originalPrice-text">
                        {selectedProduct.original_price !== null
                          ? conversorMoneda(
                              selectedProduct.original_price,
                              selectedProduct.currency_id
                            )
                          : ""}
                      </span>
                      <span className="priceWithDiscount-text">
                        {conversorMoneda(
                          selectedProduct.price,
                          selectedProduct.currency_id
                        )}
                      </span>
                    </div>

                    {selectedProduct.original_price !== null &&
                      selectedProduct.price !== null &&
                      calculateDiscountPercentage(
                        selectedProduct.original_price,
                        selectedProduct.price
                      ) > 0 && (
                        <div className="discountText_product">
                          <b>
                            <span>
                              {calculateDiscountPercentage(
                                selectedProduct.original_price,
                                selectedProduct.price
                              )}
                            </span>
                            <span>%</span>
                          </b>
                          <span className="ml-1">OFF</span>
                        </div>
                      )}
                  </div>
                  <div className="installments-with-interest_container">
                    {allProductDetails.installments !== 0 ? (
                      <div className="installments_mainContainer">
                        <span className="installments_susbtitle">
                          Nuestras PROMOS Bancarias:
                        </span>
                        <section className="installmentsQuantity-cardIcons_container">
                          <div className="installmentsQuantity_text">
                            <span style={{ fontWeight: 600, color: "#008A45" }}>
                              {allProductDetails.installments !== null &&
                                allProductDetails.installments.quantity &&
                                ""}
                            </span>
                            <span>{` cuotas `}</span>
                            {allProductDetails.installments !== null &&
                              allProductDetails.installments.rate === 0 && (
                                <span
                                  style={{
                                    color: "#008A45",
                                    fontWeight: 600,
                                  }}
                                >
                                  sin interés
                                </span>
                              )}

                            {` de ${
                              allProductDetails.installments !== null &&
                              (allProductDetails.installments.amount ||
                                allProductDetails.installments.currency_id) &&
                              conversorMoneda(
                                allProductDetails.installments.amount,
                                allProductDetails.installments.currency_id
                              )
                            }`}
                          </div>
                          <div className="cardIcons_container">
                            <img
                              className="custom-visaIcon"
                              src={visa_icon}
                              alt=""
                            />
                            <img
                              className="custom-mastercardIcon"
                              src={mastercard_icon}
                              alt=""
                            />
                            <img
                              className="custom-cabalIcon"
                              src={cabal_icon}
                              alt=""
                            />
                            <img
                              className="custom-amerExpressIcon"
                              src={amerExpress_icon}
                              alt=""
                            />
                          </div>
                        </section>
                        <section className="installments-promotionNow_container">
                          <div className="installments-promotionNow">
                            <span style={{ fontWeight: 600 }}>{`${
                              nowIcon === "ahora-3" ||
                              nowIcon === "mshops_ahora-3"
                                ? "Ahora 3 "
                                : nowIcon === "ahora-6" ||
                                  nowIcon === "mshops_ahora-6"
                                ? "Ahora 6 "
                                : (nowIcon === "ahora-12" ||
                                    "mshops_ahora-12") &&
                                  "Ahora 12 "
                            } `}</span>
                            <span>{`cuotas fijas de ${
                              allProductDetails.installments !== null &&
                              (allProductDetails.installments.amount ||
                                allProductDetails.installments.currency_id) &&
                              conversorMoneda(
                                allProductDetails.installments.amount,
                                allProductDetails.installments.currency_id
                              )
                            }`}</span>
                            <img
                              className="custom-nowIcon"
                              src={
                                nowIcon === "ahora-3" ||
                                nowIcon === "mshops_ahora-3"
                                  ? ahora3
                                  : nowIcon === "ahora-6" ||
                                    nowIcon === "mshops_ahora-6"
                                  ? ahora6
                                  : (nowIcon === "ahora-12" ||
                                      "mshops_ahora-12") &&
                                    ahora12
                              }
                              alt=""
                            />
                          </div>
                          <div className="shippingContainer">
                            {selectedProduct.shipping?.free_shipping && (
                              <div className="shipping_content">
                                <span className="shipping-text">
                                  <span style={{ fontSize: 12 }}>AMBA</span>,
                                  Envío Gratis y en 24hs
                                </span>

                                <i className="fa-solid fa-truck shipping-icon"></i>
                              </div>
                            )}
                          </div>
                          <button
                            className="buyButton"
                            onClick={handle_continueBuy}
                          >
                            Comprar ahora
                          </button>
                        </section>
                      </div>
                    ) : (
                      ""
                    )}
                  </div>
                </div>
              </div>
            </div>
          </section>
          <section className="productSpecifications_container">
            <div className="product_title-articleNum_container">
              <h5 className="product__title">{selectedProduct.title}</h5>
              <span className="numArticle-title">
                {`Artículo: ${selectedProduct.articleRandomNumber}`}
              </span>
            </div>
            <div className="technicalCharacteristics_container">
              <h5 className="technical-characteristics_text">
                {categoryPath.length >= 0 &&
                (categoryPath[0].name === "Ropa y Accesorios" ||
                  categoryPath[0].name === " Alimentos y Bebidas" ||
                  categoryPath[0].name === "Hogar, Muebles y Jardín")
                  ? "Descripción del producto"
                  : categoryPath[0].name === "Animales y Mascotas"
                  ? "Descripción del animal"
                  : "Especificaciones técnicas"}
              </h5>
              <div className="specification-container">
                <div className="specification-item first-specification">
                  {first_detailsContainer.map((item, index) => (
                    <div
                      className={`item-details ${
                        (index + 1) % 2 !== 0 && "dark-background"
                      }`}
                      key={item.id}
                    >
                      <span className="name-text">{item.name}</span>
                      <span className="valueName-text">{item.value_name}</span>
                    </div>
                  ))}
                </div>

                <div className="specification-item second-specification">
                  {second_detailsContainer.map((item, index) => (
                    <div
                      className={`item-details ${
                        (index + 1) % 2 === 0 && "dark-background"
                      }`}
                      key={item.id}
                    >
                      <span className="name-text">{item.name}</span>
                      <span className="valueName-text">{item.value_name}</span>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </section>
          <section className="sliderRelated_ProductMainContainer">
            <SliderRelatedProducts />
          </section>

          <Footer />
        </div>
      ) : (
        <Loading />
      )}
    </>
  );
};

export default ProductDetails;
